<template>
  <v-simple-table>
	  <template>
	    <thead>
	      <tr>
            <th class="text-left" v-for="(field, key) in headers" :key="key">
                {{ field.text }}  
            </th>
            <th></th>
	      </tr>
	    </thead>
	    <tbody>
        <tr v-for="(item, n) in getItems" :key="n">
  				<template v-if="item._new || item.invoiceItemId === editRowId">
	            <td v-for="(field) in getFields" :key="field.source">  
          			<template v-if="field.type == 'select'">
		          		<component
			              :key="form[field.key] ? getKey(form[field.key]) : field.source"
			              :is="`va-select-input`"
			              :source="field.source"
			              :item="item"
			              :reference="field.reference"
			              @change="setFilter($event, field.filterId, field.source)"
			              :filter="getFilters(field.filters)"
			              v-model="form[field.source]"
			              dense
			              v-bind="field.attributes"
			              :label="item._new ? ' ' : field.label"
			              :filled="field.filled ? field.filled : false"
			              class="mt-6"
		          		></component>
          			</template>
          			<template v-else>
		              <component
		                :key="field.source"
		                :is="`va-${field.input || field.type || 'text'}-input`"
		                :item="item"
		                v-model="form[field.source]"
		                dense
		                :filled="field.filled ? field.filled : false"
		                :label="(item._new | field.type == 'boolean') ? ' ' : field.label"
		                v-bind="checkProperty(field, 'options', 'source') ? getOptions(field.options, form[field.options.source]) : field.attributes"
		                class="mt-6"
		              ></component>
          			</template>
	            </td>
	            <td>
	                <v-icon small class="mr-2" @click="editItem(item)">
	                  mdi-pencil
	                </v-icon>
	                <v-icon small @click="deleteItem(item)">
	                  mdi-delete
	                </v-icon>
	            </td>
            </template>
            <template v-else>
        			<td v-for="(field) in getFields" :key="field.source">
        				{{ field.type == 'select' ? item[field.sourceLabel] : item[field.source] }}
        			</td>	
	            <td>
	                <v-icon small class="mr-2" @click="editItem(item)">
	                  mdi-pencil
	                </v-icon>
	                <v-icon small @click="deleteItem(item)">
	                  mdi-delete
	                </v-icon>
	            </td>
            </template>
        </tr>
	   	</tbody>
	  </template>
	</v-simple-table>
</template>

<script>
import size from "lodash/size";

export default {
	props: {
	    items: {
	      type: Array,
	      default: () => [],
	    },
	    /**
	     * List of columns for each property of resource data.
	     * Each column can be a simple string or a full object with advanced field properties.
	     * Valid properties are `source`, `type`, `label`, `sortable`, `align`, `link`, `attributes`, `editable`.
	     */
	    fields: {
	      type: Array,
	      default: () => [],
	    },
	},
	data() {
	    return {
	    	filterIds: {},
	    	editRowId: null,
      	form: null,
	    };
	},
	methods: {
    setFilter(event, filterId){ 
      // console.error(event)
      if (filterId) {
        if (Array.isArray(event)) { // if multiple select input
          let ids = []
          for (let i = 0; i < event.length; i++) {
              ids.push(event[i].id)
          }
          this.filterIds[filterId] = ids.join()  
        } else {   // else standart select input
          this.filterIds[filterId] = event.id
        }
        // console.error(this.filterIds)
      }
      // if (typeof this.$v['form'][source] != "undefined") {
      //   this.$v['form'][source].touch
      // }
      return
    },
    getFilters(filters) {
      if (filters) {
        let filterObject = {}
        if (size(this.filterIds) > 0) {
          for (var key in this.filterIds) {
              if (filters.includes(key)) {
                  filterObject[key] = this.filterIds[key]
              }
          }
        }
        // console.error(filterObject)
        return filterObject;
      }
      return {}     
    },
    getKey(keyValue, index = 0) {
      if (Array.isArray(keyValue)) {
          let ids = []
          for (let i = 0; i < keyValue.length; i++) {
              ids.push(keyValue[i].id)
          }
          return ids.join() + '-' + String(index)
      }
      return keyValue + '-' + String(index)
    },
    checkProperty(field, prop1, prop2) {
      if (Object.prototype.hasOwnProperty.call(field, prop1)) {
        if (Object.prototype.hasOwnProperty.call(field[prop1], prop2)) {
           return true
        }
      }
      return false
    },
    editItem(item, disableRowEdit = null) {
      if (disableRowEdit == null) {
        this.createRowForm(item);
        return;
      }
    },
    createItem(item) {
      this.createRowForm(item, "new");
    },
    createRowForm(item = null, action = null) {
      if (action == "new") {
        this.editRowId = null
      } else {
        this.editRowId = item ? item.invoiceItemId : null;  
      }
      this.form = this.getFields
        .map((f) => f.source)
        .reduce((o, source) => {
          return {
            ...o,
            [source]: item ? item[source] : null,
          };
        }, {});
    },
	},
	computed: {
		 getItems() {
      if (this.form && !this.editRowId) {
        return [{ _new: true }, ...this.items];
      }
      if (this.items.length > 0) { // add index numbers
        return this.items.map((data, index) => ({
          ...data,
          index: index
        }))  
      }
      return this.items;
    },
    headers() {
      let fields = this.getFields.map((field) => {
        return {
          text: field.label,
          value: field.source
        };
      });
      return fields;
    },
    getFields() {
      return this.fields
        .map((f) => {
          return typeof f === "string"
            ? {
                source: f,
              }
            : f;
        })
        .map((f) => {
          return {
            ...f,
            type: f.type,
            label: f.label
          };
        });
    },

	}
}
</script>