<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-row>
          <v-col sm="4">
            <va-text-input
              v-model="model.subDepartmentName"
              :label="$t('departments.subDepartmentName')"
            ></va-text-input>
          </v-col>
          <v-col sm="4">
            <va-text-input
              v-show="false"
              source="model.subDepartmentId"
              v-model="model.subDepartmentId"
              :label="$t('departments.subDepartmentName')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="4">
            <va-text-input
              v-model="model.subDepartmentManagerName"
              :label="$t('departments.subDepartmentManagerName')"
              :error-messages="subDepartmentManagerNameErrors"
            ></va-text-input>
          </v-col>
          <v-col sm="4">
            <va-text-input
              v-model="model.subDepartmentManagerSurname"
              :label="$t('departments.subDepartmentManagerSurname')"
              :error-messages="subDepartmentManagerSurnameErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="4">
            <va-select-input
              source="subDepartmentManagerPhoneAreaCodeId"
              v-model="model.subDepartmentManagerPhoneAreaCodeId"
              reference="areaCodes"
            ></va-select-input>
          </v-col>
          <v-col sm="4">
            <va-text-input
              v-model="model.subDepartmentManagerPhone"
              :label="$t('departments.subDepartmentManagerPhone')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="4">
            <va-select-input
              source="subDepartmentManagerMobileAreaCodeId"
              v-model="model.subDepartmentManagerMobileAreaCodeId"
              reference="areaCodes"
            ></va-select-input>
          </v-col>
          <v-col sm="4">
            <va-text-input
              v-model="model.subDepartmentManagerMobile"
              :label="$t('departments.subDepartmentManagerMobile')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="8">
            <va-text-input
              v-model="model.subDepartmentManagerEmail"
              :label="$t('departments.subDepartmentManagerEmail')"
            ></va-text-input>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="12">
        <v-btn color="primary" dark class="mb-2" @click="save">
          {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: ["edit", "departmentId"],
  data() {
    return {
      model: {
        departmentId: this.deparmentId,
        subDepartmentId: "",
        subDepartmentName: "",
        subDepartmentManagerName: "",
        subDepartmentManagerSurname: "",
        subDepartmentManagerPhoneAreaCodeId: "TR",
        subDepartmentManagerPhone: "",
        subDepartmentManagerMobileAreaCodeId: "TR",
        subDepartmentManagerMobile: "",
        subDepartmentManagerEmail: "",
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["departments/getDialogItem"];
      },
      set(model) {
        this.$store.commit(
          `departments/setDialogItem`,
          Object.assign({}, model)
        );
      },
    },
    subDepartmentManagerNameErrors() {
      const errors = [];
      const field = "subDepartmentManagerName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    subDepartmentManagerSurnameErrors() {
      const errors = [];
      const field = "subDepartmentManagerSurname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
  },
  validations: {
    model: {
      subDepartmentManagerName: {
        required,
      },
      subDepartmentManagerSurname: {
        required,
      },
    },
  },
  methods: {
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
