<script>
import { generateId } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      // https://codepen.io/scp-nm/pen/LYpBeoe
      e1: 1,
      tabs: null,
      currencyId: "TRY",
      allowanceCurrencyId: "TRY",
      renderKeyAllowances: 1001,
      model: {
        id: null,
        versionName: null,
        sowId: null,
        customerId: null,
        departmentId: "",
        subDepartmentId: "",
        employeeId: null,
        currencyId: null,
        rateSeniorityBase: "0",
        rateNoticePay: "0",
        rateAllowance: "0",
        rateCommission: "0",
        rateIsgCost: "0",
        rateReturn: "0",
        employeeCostConfirms: {
          isConfirmed: false,
          confirmedDepartmentId: null,
          confirmedPurchaserId: null,
          confirmDate: null,
        },
        result: {
          resultSeniorityPayment: "0",
          resultNoticePayment: "0",
          resultAllowancePayment: "0",
          resultSGKAmount: "0",
          resultEmployeeCost: "0",
          resultComissionCost: "0",
          resultStableComissionCost: "0",
          resultFinancialCost: "0",
          resultTax: "0",
          resultAdditionalCost: "0",
          resultTotalCost: "0",
        },
        calculationDate: "",
        employeeCostAllowanceSheet: null,
        employeeCostAdditionalPays: null,
      },
      employeeCostAllowanceSheet: [],
      allowanceDialog: false,
      allowanceDialogDelete: false,
      allowanceEditedIndex: -1,
      allowanceEditedItem: {
        allowanceId: "",
        allowanceName: "",
        allowanceAmount: "",
        allowanceCurrencyId: "",
        allowanceSalaryTypeId: "",
        allowanceExceptPayroll: false,
      },
      allowanceDefaultItem: {
        allowanceId: "",
        allowanceName: "",
        allowanceAmount: "",
        allowanceCurrencyId: "TRY",
        allowanceSalaryTypeId: "gross",
        allowanceExceptPayroll: false,
      },
      allowanceHeaders: [
        {
          text: this.$t("costs.allowanceName"),
          value: "allowanceName",
        },
        {
          text: this.$t("costs.allowanceAmount"),
          value: "allowanceAmount",
        },
        {
          text: this.$t("costs.allowanceCurrencyId"),
          value: "allowanceCurrencyId",
        },
        {
          text: this.$t("costs.allowanceSalaryTypeId"),
          value: "allowanceSalaryTypeId",
        },
        {
          text: this.$t("costs.allowanceExceptPayroll"),
          value: "allowanceExceptPayroll",
        },
        {
          text: this.$t("costs.allowanceSheetActions"),
          value: "actions",
          sortable: false,
        },
      ],
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 2,
        masked: false,
      } /* doesn't work with directive */,
      rate: {
        decimal: "",
        thousands: "",
        prefix: "",
        suffix: " %",
        precision: 0,
        masked: false,
      },
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: this.$t("costs.additionalPayName"),
          value: "additionalPayName",
        },
        {
          text: this.$t("costs.additionalPayAmount"),
          value: "additionalPayAmount",
        },
        {
          text: this.$t("costs.additionalPayCurrencyId"),
          value: "additionalPayCurrencyId",
        },
        {
          text: this.$t("costs.additionalPayActions"),
          value: "actions",
          sortable: false,
        },
      ],
      additionalPayCurrencyId: "TRY",
      employeeCostAdditionalPays: [],
      editedIndex: -1,
      editedItem: {
        additionalPayId: "",
        additionalPayName: "",
        additionalPayCurrencyId: "",
        additionalPayAmount: "0",
      },
      defaultItem: {
        additionalPayId: "",
        additionalPayName: "",
        additionalPayCurrencyId: "TRY",
        additionalPayAmount: "0",
      },
    };
  },
  validations: {
    model: {
      versionName: {
        required,
        maxLength: maxLength(100),
      },
      sowId: {
        required,
        maxLength: maxLength(36),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      departmentId: {
        required,
        maxLength: maxLength(36),
      },
      subDepartmentId: {
        maxLength: maxLength(36),
      },
      employeeId: {
        required,
        maxLength: maxLength(36),
      },
      currencyId: {
        required,
        maxLength: maxLength(3),
      },
      employeeCostConfirms: {
        confirmedDepartmentId: {
          maxLength: maxLength(36),
        },
        confirmedPurchaserId: {
          maxLength: maxLength(36),
        },
      },
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    allowanceDialog(val) {
      this.allowanceRenderModalComponent();
      val || this.allowanceClose();
    },
    allowanceDialogDelete(val) {
      val || this.allowanceCloseDelete();
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    allowanceFormTitle() {
      return this.allowanceEditedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    versionNameErrors() {
      const errors = [];
      const field = "versionName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    departmentIdErrors() {
      const errors = [];
      const field = "departmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    subDepartmentIdErrors() {
      const errors = [];
      const field = "subDepartmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    employeeIdErrors() {
      const errors = [];
      const field = "employeeId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    currencyIdErrors() {
      const errors = [];
      const field = "currencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "3" }));
      return errors;
    },
    confirmedDepartmentIdErrors() {
      const errors = [];
      const field = "confirmedDepartmentId";
      if (!this.$v["model"]["employeeCostConfirms"][field].$dirty)
        return errors;
      !this.$v["model"]["employeeCostConfirms"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    confirmedPurchaserIdErrors() {
      const errors = [];
      const field = "confirmedPurchaserId";
      if (!this.$v["model"]["employeeCostConfirms"][field].$dirty)
        return errors;
      !this.$v["model"]["employeeCostConfirms"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    this.initializeEmployeeAllowanceSheet();
    this.initializeAdditionalPays();
  },
  async mounted() {
    if (this.item == null) {
      let response = await this.$admin.http.get(
        "/employees/findEmployeeCostAllowanceSheet"
      );
      this.employeeCostAllowanceSheet = response.data.data;
      return;
    }
  },
  methods: {
    initializeEmployeeAllowanceSheet() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(
          this.item,
          "employeeCostAllowanceSheet"
        )
      ) {
        this.model.employeeCostAllowanceSheet = this.item.employeeCostAllowanceSheet;
        this.employeeCostAllowanceSheet = JSON.parse(
          this.item.employeeCostAllowanceSheet
        );
      }
    },
    initializeAdditionalPays() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(
          this.item,
          "employeeCostAdditionalPays"
        )
      ) {
        this.model.employeeCostAdditionalPays = this.item.employeeCostAdditionalPays;
        this.employeeCostAdditionalPays = JSON.parse(
          this.item.employeeCostAdditionalPays
        );
      }
    },
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.model.currencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.model.currencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    detectAdditionalPayCurrencyId() {
      if (this.editedItem.additionalPayCurrencyId == "TRY") {
        this.additionalPayCurrencyId = "TRY";
      }
      if (this.editedItem.additionalPayCurrencyId == "EUR") {
        this.additionalPayCurrencyId = "EUR";
      }
      if (this.editedItem.additionalPayCurrencyId == "USD") {
        this.additionalPayCurrencyId = "USD";
      }
    },
    detectAllowanceCurrencyId() {
      if (this.allowanceEditedItem.allowanceCurrencyId == "TRY") {
        this.allowanceCurrencyId = "TRY";
      }
      if (this.allowanceEditedItem.allowanceCurrencyId == "EUR") {
        this.allowanceCurrencyId = "EUR";
      }
      if (this.allowanceEditedItem.allowanceCurrencyId == "USD") {
        this.allowanceCurrencyId = "USD";
      }
    },
    onValidate() {
      if (
        this.$v["model"]["departmentId"].$invalid ||
        this.$v["model"]["employeeId"].$invalid
      ) {
        this.e1 = 1;
      }
    },
    onSaved() {
      if (this.e1 < 2) {
        this.e1 = 2;
        return;
      }
      if (this.e1 < 3) {
        this.e1 = 3;
        return;
      }
      if (this.e1 < 4) {
        this.e1 = 4;
        return;
      }
      if (this.e1 < 5) {
        this.e1 = 5;
        return;
      }
    },
    editItem(item) {
      this.editedIndex = this.employeeCostAdditionalPays.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.model.employeeCostAdditionalPays = JSON.stringify(
        this.employeeCostAdditionalPays
      );
      this.childrenEditWindow = true;
      this.dialog = true;
    },
    deleteItem(item) {
      this.editedIndex = this.employeeCostAdditionalPays.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.employeeCostAdditionalPays.splice(this.editedIndex, 1);
      this.model.employeeCostAdditionalPays = JSON.stringify(
        this.employeeCostAdditionalPays
      );
      this.closeDelete();
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(
          this.employeeCostAdditionalPays[this.editedIndex],
          this.editedItem
        );
      } else {
        this.employeeCostAdditionalPays.push(this.editedItem);
      }
      this.model.employeeCostAdditionalPays = JSON.stringify(
        this.employeeCostAdditionalPays
      );
      this.close();
    },
    allowanceRenderModalComponent() {
      this.renderKeyAllowances = this.renderKeyAllowances + 1;
    },
    allowanceEditItem(item) {
      this.allowanceEditedIndex = this.employeeCostAllowanceSheet.indexOf(item);
      this.allowanceEditedItem = Object.assign({}, item);
      this.model.employeeCostAllowanceSheet = JSON.stringify(
        this.employeeCostAllowanceSheet
      );
      this.allowanceDialog = true;
      this.$store.commit(
        `costs/setDialogItem`,
        Object.assign({}, this.allowanceEditedItem)
      );
    },
    allowanceDeleteItem(item) {
      this.allowanceEditedIndex = this.employeeCostAllowanceSheet.indexOf(item);
      this.allowanceEditedItem = Object.assign({}, item);
      this.allowanceDialogDelete = true;
    },
    allowanceDeleteItemConfirm() {
      this.employeeCostAllowanceSheet.splice(this.allowanceEditedIndex, 1);
      this.model.employeeCostAllowanceSheet = JSON.stringify(
        this.employeeCostAllowanceSheet
      );
      this.allowanceCloseDelete();
    },
    allowanceClose() {
      this.allowanceDialog = false;
      this.$nextTick(() => {
        this.allowanceEditedItem = Object.assign({}, this.allowanceDefaultItem);
        this.allowanceEditedIndex = -1;
      });
    },
    allowanceCloseDelete() {
      this.allowanceDialogDelete = false;
      this.$nextTick(() => {
        this.allowanceEditedItem = Object.assign({}, this.allowanceDefaultItem);
        this.allowanceEditedIndex = -1;
      });
    },
    allowanceModalSave(allowanceEditedItem) {
      this.allowanceEditedItem = allowanceEditedItem;
      this.allowanceSave();
    },
    allowanceSave() {
      if (this.allowanceEditedIndex > -1) {
        Object.assign(
          this.employeeCostAllowanceSheet[this.allowanceEditedIndex],
          this.allowanceEditedItem
        );
      } else {
        this.employeeCostAllowanceSheet.push(this.allowanceEditedItem);
      }
      this.model.employeeCostAllowanceSheet = JSON.stringify(
        this.employeeCostAllowanceSheet
      );
      this.allowanceClose();
    },
  },
};
</script>
