<template>
  <div>
    <v-row>
      <v-col sm="12" md="12">
        <va-text-input
          :label="$t('users.newPassword')"
          v-model="model.password"
          :error-messages="passwordErrors"
        ></va-text-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="12">
        <v-btn color="primary" dark class="mb-2" @click="save" :loading="passwordUpdating">
          {{ $t("dialog.saveButton") }}
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: ["id"],
  data() {
    return {
      passwordUpdating: false,
      model: {
        password: null,        
      },
    };
  },
  computed: {
    passwordErrors() {
      const errors = [];
      const field = "password";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required && errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    model: {
      password: {
        required,
      },
    },
  },
  methods: {
    async updatePassword() {
      this.passwordUpdating = true;
      try {
        let url = "/users/updatePassword/" + this.id;
        let method = "put";
        let data = { password: this.model.password }
        await this.$admin.http({ method, url, data })
      } 
      // catch (e) {
      //   this.$snotify.warning(e.message);
      // } 
      finally {
        this.passwordUpdating = false;
      }
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.updatePassword()
      this.$emit("save");
    },
  },
};
</script>
