<template>
<div>
  <v-form ref="form" @submit.prevent="validate">
    <v-text-field
      :label="$t('auth.username')"
      prepend-icon="mdi-account"
      v-model="username"
      required
      :error-messages="emailErrors"
      @input="$v.username.$touch()"
    ></v-text-field>

    <v-text-field
      :label="$t('auth.password')"
      prepend-icon="mdi-lock"
      type="password"
      v-model="password"
      required
      :error-messages="passwordErrors"
      @input="$v.password.$touch()"
    ></v-text-field>

    <!-- https://vuetifyjs.com/en/styles/spacing/#how-it-works -->
    <v-row>
      <v-col sm="12" class="pa-3"></v-col>
    </v-row>

    <v-row>
      <v-col sm="12">
        <div class="text-right">
          <v-btn
            :loading="loading"
            color="primary"
            large
            type="submit"
            text
            rounded
            >{{ $t("auth.signIn") }}</v-btn
          >
        </div>
      </v-col>
    </v-row>
  </v-form>
</div>
</template>

<script>
// https://codepen.io/paulpv/pen/qoKoKV
//
import { mapActions } from "vuex";
import {
  required,
  email,
  minLength,
  maxLength,
} from "vuelidate/lib/validators";

export default {
  data() {
    return {
      username: "",
      password: "",
      loading: false,
    };
  },
  validations: {
    username: {
      required,
      email,
    },
    password: {
      required,
      minLength: minLength(8),
      maxLength: maxLength(16),
    },
  },
  computed: {
    emailErrors() {
      const errors = [];
      if (!this.$v.username.$dirty) return errors;
      !this.$v.username.email && errors.push(this.$t("v.email.invalid"));
      !this.$v.username.required && errors.push(this.$t("v.email.required"));
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.minLength &&
        errors.push(this.$t("v.string.minLength", { min: "8" }));
      !this.$v.password.maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "16" }));
      !this.$v.password.required && errors.push(this.$t("v.password.required"));
      return errors;
    },
  },
  methods: {
    ...mapActions({
      login: "auth/login",
    }),
    async validate() {
      this.$v.$touch();
      this.loading = false;
      if (this.$v.$invalid) {
        return;
      }
      this.loading = true;
      try {
        await this.login({ username: this.username, password: this.password });
      } catch (e) {
        this.loading = false;
      }
    },
    clear() {
      this.$v.$reset();
      this.username = "";
      this.password = "";
    },
  },
};
</script>
