<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-row>
          <v-col sm="12">
            <va-select-input
              reference="jobTitles"
              v-model="model.jobTitleId"
              :label="$t('employees.jobTitleId')"
              :error-messages="jobTitleIdErrors"
            ></va-select-input>
          </v-col>
          <v-col sm="12">
            <va-select-input
              reference="healthdocs"
              v-model="model.healthDocId"
              :filter="{ jobTitleId: item.jobTitleId }"
              :key="item.jobTitleId"
              :label="$t('employees.healthDocId')"
              @change="setHealthDocName($event)"
              :error-messages="healthDocIdErrors"
            ></va-select-input>
            <va-text-input
              v-show="true"
              v-model="model.healthDocName"
              :label="$t('employees.healthDocId')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-date-input
              v-model="model.healthDocDate"
              :label="$t('employees.healthDocDate')"
              @change="setValidityDate"
              format="short"
              :error-messages="healthDocDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-date-input
              v-model="model.healthDocDoctorDate"
              :label="$t('employees.healthDocDoctorDate')"
              format="short"
              :error-messages="healthDocDoctorDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-text-input
              v-model="model.healthDocValidityEnd"
              v-mask="'####-##-##'"
              :label="$t('employees.healthDocValidityEnd')"
              :error-messages="healthDocValidityEndErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-btn color="primary" dark class="mb-2" @click="save">
              {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { dateAddMonth } from "../../js/common";
import {
  required,
  // email,
  // minLength,
  // maxLength,
} from "vuelidate/lib/validators";

export default {
  props: ["edit"],
  data() {
    return {
      validity: null,
      model: {
        jobTitleId: null,
        healthDocId: null,
        healthDocName: null,
        healthDocDate: null,
        healthDocDoctorDate: null,
        healthDocValidityEnd: null,
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["employees/getDialogItem"];
      },
      set(model) {
        this.$store.commit("employees/setDialogItem", Object.assign({}, model));
      },
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    healthDocIdErrors() {
      const errors = [];
      const field = "healthDocId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    healthDocDateErrors() {
      const errors = [];
      const field = "healthDocDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    healthDocDoctorDateErrors() {
      const errors = [];
      const field = "healthDocDoctorDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    healthDocValidityEndErrors() {
      const errors = [];
      const field = "healthDocValidityEnd";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
  },
  validations: {
    model: {
      jobTitleId: {
        required,
      },
      healthDocId: {
        required,
      },
      healthDocDate: {
        required,
      },
      healthDocDoctorDate: {
        required,
      },
      healthDocValidityEnd: {
        required,
      },
    },
  },
  methods: {
    setHealthDocName(event) {
      this.model.healthDocName = event.name;
      this.validity = event.validity;
    },
    setValidityDate() {
      this.model.healthDocValidityEnd = dateAddMonth(
        this.model.healthDocDate,
        this.validity
      );
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
