  watch: {
    dialog(val) {
      this.renderComponent("Children");
      val || this.close();
    },
    educationDialog(val) {
      this.renderComponent("Educations");
      val || this.educationClose();
    },
    healthDocDialog(val) {
      this.renderComponent("Healthdocs");
      val || this.healthDocClose();
    },
    debitDialog(val) {
      this.renderComponent("Debits");
      val || this.debitClose();
    },
    agreementDialog(val) {
      this.renderComponent("Agreements");
      if (val == true) {
        if (this.agreementEditedItem.customerId == null) {
          this.agreementEditedItem.customerId = this.model.customerId;
          this.agreementEditedItem.customerName = this.model.customerName;
        }
      }
      val || this.agreementClose();
    },
    docDialog(val) {
      if (val == true) {
        if (this.docEditedItem.fieldId == "") {
          this.docEditedItem.fieldId = generateColumnName(6);
          this.docEditedItem.fieldNew = true;
        }
      }
      val || this.docClose();
    },
