<template>
  <div>
    <div v-if="!loading && !showResults && !showSaveResults">
      <v-row>
        <v-col sm="12">
          <v-row>
            <v-col sm="8">
              <va-select-input
                v-model="model.customerId"
                reference="customers"
                :label="$t('expenses.customerId')"
                @change="setCustomerName($event)"
                :error-messages="customerIdErrors"
              ></va-select-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="8">
              <va-select-input
                  v-model="model.workplaceId"
                  reference="workplaces"
                  :label="$t('expenses.workplaceId')"
                  @change="setWorkplaceName($event)"
                  :error-messages="workplaceIdErrors"
              ></va-select-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="8">
              <va-select-input
                v-model="model.sowId"
                reference="sows"
                :key="model.customerId"
                :filter="{ customerId: model.customerId }"
                :label="$t('expenses.sowId')"
                @change="setSowName($event)"
                :error-messages="sowIdErrors"
              ></va-select-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="8">
                <va-select-input
                  v-model="model.departmentId"
                  :key="model.customerId + '_department'"
                  :filter="{ customerId: model.customerId }"
                  :label="$t('expenses.departmentId')"
                  @change="setDepartmentName($event)"
                  reference="departments"
                ></va-select-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="8">
                <va-select-input
                  v-model="model.subDepartmentId"
                  :key="model.departmentId + '_subDepartment'"
                  :filter="{ departmentId: model.departmentId }"
                  :label="$t('expenses.subDepartmentId')"
                  @change="setSubDepartmentName($event)"
                  reference="subDepartments"
                ></va-select-input>
            </v-col>
          </v-row>
          <v-row>
            <v-col sm="8">
                <va-file-input
                  v-model="model.file"
                  :label="$t('expenses.excelImportFile')"
                  accept=".xls,.xlsx"
                  base64
                  lg="4"
                  :error-messages="fileErrors"
                >
                </va-file-input>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col sm="12">
          <v-btn color="primary" :loading="loading" dark class="mb-2" @click="importExcel">
            {{ $t("dialog.importExcelButton") }}
          </v-btn>
        </v-col>
      </v-row>
    </div>
    <template v-if="loading">
        <div class="text-center">
          <v-progress-circular
            indeterminate
            :color="progressColor"
          ></v-progress-circular>
        </div>
    </template>
    <div v-if="showResults && !showSaveResults">
      <template>
        <v-simple-table>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">No.</th>
                <th class="text-left">{{ $t("expenses.customerId") }}</th>
                <th class="text-left">{{ $t("expenses.workplaceId") }}</th>
                <th class="text-left">{{ $t("expenses.sowId") }}</th>
                <th class="text-left">{{ $t("expenses.departmentId") }}</th>
                <th class="text-left">{{ $t("expenses.subDepartmentId") }}</th>
                <th class="text-left">{{ $t("expenses.employeeId") }}</th>
                <th class="text-left">{{ $t("expenses.expenseTypeId") }}</th>
                <th class="text-left">{{ $t("expenses.description") }}</th>
                <th class="text-left">{{ $t("expenses.expenseDate") }}</th>
                <th class="text-left">{{ $t("expenses.docNumber") }}</th>
                <th class="text-left">{{ $t("expenses.currencyId") }}</th>
                <th class="text-left">{{ $t("expenses.amount") }}</th>
                <th class="text-left">{{ $t("expenses.tax") }}</th>
                <th class="text-left">{{ $t("expenses.totalAmount") }}</th>
                <th class="text-left">{{ $t("expenses.whoPaidId") }}</th>
                <th class="text-left">{{ $t("expenses.paymentTypeId") }}</th>
                <th class="text-left">{{ $t("expenses.paidBoolean") }}</th>
                <th class="text-left">{{ $t("expenses.expenseApplication") }}</th>
                <th class="text-left">{{ $t("expenses.packageNo") }}</th>
<!--                 <th class="text-left">{{ $t("expenses.confirmStatus") }}</th> -->
                <th class="text-left">{{ $t("expenses.file") }}</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(val, n) in results"
                :key="val.key"
              >
                <td>{{ n }}</td>
                <td>{{ val.customerName }}</td>
                <td>{{ val.workplaceName }}</td>
                <td>{{ val.sowName }}</td>
                <td>{{ val.departmentName }}</td>
                <td>{{ val.subDepartmentName }}</td>
                <td>
                  <va-select-input
                    v-model="form.model[n].employeeId"
                    :key="val.pernetNumber"
                    reference="employees"
                    hide-details
                  ></va-select-input>
                </td>
                <td>
                  <va-select-input
                    v-model="form.model[n].expenseTypeId"
                    reference="expenseTypes"
                    hide-details
                  ></va-select-input>
                </td>
                <td>{{ val.description }}</td>
                <td>{{ new Date(val.expenseDate).toLocaleDateString("tr") }}</td>
                <td>{{ val.docNumber }}</td>
                <td>{{ val.currencyId }}</td>
                <td>{{ val.amount }}</td>
                <td>{{ val.tax }}</td>
                <td>{{ val.totalAmount }}</td>
                <td>
                  <va-select-input
                    source="whoPaidId"
                    v-model="form.model[n].whoPaidId"
                    hide-details
                  ></va-select-input>
                </td>
                <td>
                  <va-select-input
                    v-model="form.model[n].paymentTypeId"
                    reference="paymentTypes"
                    :label="$t('expenses.paymentTypeId')"
                    hide-details
                  ></va-select-input>
                </td>
                <td v-if="form.model[n].whoPaidId == 'employee'">
                  <va-boolean-input source="isEmployeePaid" v-model="form.model[n].isEmployeePaid"></va-boolean-input>
                </td>
                <td v-if="form.model[n].whoPaidId == 'pernet'">
                  <va-boolean-input source="isSellerPaid" v-model="form.model[n].isSellerPaid"></va-boolean-input>
                </td>
                <td><va-text-input v-model="form.model[n].expenseApplication" :label="$t('expenses.expenseApplication')"></va-text-input></td>
                <td><va-text-input v-model="form.model[n].packageNo" :label="$t('expenses.packageNo')"></va-text-input></td>
<!--                 <td><va-boolean-input v-model="form.model[n].confirmStatus" :label="$t('expenses.confirmStatus')"></va-boolean-input></td> -->
                <td>{{ val.file.name }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </template>
      <v-row>
        <v-col sm="12">
          <v-btn color="primary" dark class="mb-2" @click="saveImport">
            {{ $t("dialog.saveButton") }}
          </v-btn>
        </v-col>
      </v-row>
    </div>
    <div v-if="showSaveResults">
      <v-alert v-if="affectedRows > 0"
        dense
        text
        type="success"
      >{{ affectedRows }} adet kayt baaryla kayt edildi</v-alert>
      <v-alert v-else
        dense
        text
        type="warning"
      >Kayt baarsz oldu</v-alert>
    </div>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: [],
  data() {
    return {
      results: null,
      showResults: false,
      showSaveResults: false,
      progressColor: null,
      loading: false,
      affectedRows: 0,
      interval: {},
      loadedValue: 0,
      form: {
        model: []
      },
      model: {
        customerId: '',
        employeeId: null,
        customerName: null,
        workplaceId: '',
        workplaceName: null,
        sowId: null,
        sowName: null,
        departmentId: null,
        departmentName: null,
        subDepartmentId: null,
        subDepartmentName: null,
        file: null,
      },
    };
  },
  mounted() {
    this.setFormModel()
    this.progressColor = localStorage.getItem('themeColor')
  },
  computed: {
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    workplaceIdErrors() {
      const errors = [];
      const field = "workplaceId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    fileErrors() {
      const errors = [];
      const field = "file";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  validations: {
    model: {
      customerId: {
        required,
      },
      workplaceId: {
        required,
      },
      sowId: {
        required,
      },
      file: {
        required,
      },
    },
  },
  methods: {
    setFormModel() {
      for (var i=0;i<30;i++) {
        this.form.model.push({ 
            customerId: null,
            workplaceId: null,
            sowId: null,
            departmentId: null,
            subDepartmentId: null,
            employeeId: null,
            expenseTypeId:null,
            description:null,
            expenseDate:null,
            docNumber:null,
            currencyId:null,
            amount:null,
            tax:null,
            totalAmount:null,
            whoPaidId:null,
            paymentTypeId: null,
            expenseApplication: null,
            packageNo: null,
            isEmployeePaid: null,
            isSellerPaid: null,
            confirmStatus: null,
            file: null,
            tempFile: null,
        })
      }
    },
    async importExcel() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.loading = true
      try {
        let response = await this.postImport("post", "/expenses/import", this.model)
        if (response.status == 200) {
          this.loading = false
          this.results = response.data.data.expenses
          if (typeof this.results[1].employeeId !== 'undefined') {
            this.model.employeeId = this.results[1].employeeId  
          }
          this.showResults = true
          this.$emit("width", "100%")
        }
      } finally {
        this.loading = false;
      }
    },
    async saveImport() {
      this.loading = true
      try {
        let response = await this.postCreate("post", "/expenses/createFromImport", this.form.model)
        if (response.status == 200) {
          this.loading = false
          this.affectedRows = response.data.data.affectedRows
          this.showSaveResults = true
          this.$emit("width", "600px")
        }
      } finally {
        this.loading = false;
      }
    },
    setCustomerName(event) {
      this.model.customerName = event.name
    },
    setWorkplaceName(event) {
      this.model.workplaceName = event.name
    },
    setSowName(event) {
      this.model.sowName = event.name
    },
    setDepartmentName(event) {
      this.model.departmentName = event.name
    },
    setSubDepartmentName(event) {
      this.model.subDepartmentName = event.name
    },
    async postImport(method, url, data) {
      let response = null
      try {
        response = await this.$admin.http({ method, url, data });
        for (var i = 1; i <= Object.keys(response.data.data.expenses).length; i++) {
          let obj = response.data.data.expenses[1]
          this.form.model[i].customerId = obj.customerId
          this.form.model[i].workplaceId = obj.workplaceId
          this.form.model[i].sowId = obj.sowId
          this.form.model[i].departmentId = obj.departmentId
          this.form.model[i].subDepartmentId = obj.subDepartmentId
          this.form.model[i].employeeId = obj.employeeId
          this.form.model[i].description = obj.description
          this.form.model[i].expenseDate = obj.expenseDate
          this.form.model[i].docNumber = obj.docNumber
          this.form.model[i].currencyId = obj.currencyId
          this.form.model[i].amount = obj.amount
          this.form.model[i].tax = obj.tax
          this.form.model[i].totalAmount = obj.totalAmount
          this.form.model[i].whoPaidId = obj.whoPaidId
          this.form.model[i].paymentTypeId = "4c062b67-2ea6-4d8d-9bb2-62d4f99b5498"
          this.form.model[i].isEmployeePaid = 0
          this.form.model[i].isSellerPaid = 0
          this.form.model[i].confirmStatus = 0
          this.form.model[i].file = obj.file
          this.form.model[i].tempFile = obj.tempFile
        }
        await this.sleep(500)
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return response
    },
    async postCreate(method, url, data) {
      let response = null
      try {
        response = await this.$admin.http({ method, url, data });
      } catch (e) {
        this.$snotify.warning(e.message);
      }
      return response
    },
    async sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms));
    }

  },
};
</script>

<style scoped>
.v-progress-circular {
  margin: 1rem;
}
</style>