<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-col sm="9">
          <va-text-input
            v-show="false"
            :label="$t('employees.employeeAgreements.agreementId')"
            v-model="model.agreementId"
          ></va-text-input>
          <va-text-input
            v-show="false"
            :label="$t('employees.employeeAgreements.customerId')"
            v-model="model.customerName"
          ></va-text-input>
        </v-col>
        <v-row>
          <v-col sm="4">
            <va-select-input
              :filter="{ customerId: model.customerId }"
              :label="$t('employees.employeeAgreements.sowId')"
              reference="sows"
              v-model="model.sowId"
              :error-messages="sowIdErrors"
            >
            </va-select-input>
          </v-col>
          <v-col sm="2">
            <va-text-input
              :label="$t('employees.employeeAgreements.customerEmployeeNo')"
              v-model="model.customerEmployeeNo"
            ></va-text-input>
          </v-col>
          <v-col sm="4">
            <va-text-input
              v-show="false"
              :label="$t('employees.employeeAgreements.jobTitle')"
              v-model="model.jobTitleName"
            ></va-text-input>
            <va-select-input
              reference="jobTitles"
              v-model="model.jobTitleId"
              @change="setJobTitleName($event)"
              :label="$t('employees.employeeAgreements.jobTitle')"
              :error-messages="jobTitleIdErrors"
            ></va-select-input>
          </v-col>
          <v-col sm="2">
            <va-text-input
              v-show="false"
              :label="$t('employees.employeeAgreements.agreementTypeId')"
              v-model="model.agreementTypeName"
            ></va-text-input>
            <va-select-input
              reference="agreementTypes"
              v-model="model.agreementTypeId"
              @change="setAgreementTypeName($event)"
              :label="$t('employees.employeeAgreements.agreementTypeId')"
              :error-messages="agreementTypeIdErrors"
            ></va-select-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="3">
            <va-text-input
              v-show="false"
              :label="$t('employees.employeeAgreements.departmentId')"
              v-model="model.departmentName"
            ></va-text-input>
            <va-select-input
              v-model="model.departmentId"
              reference="departments"
              :filter="{ customerId: customerId }"
              @change="setDepartmentName($event)"
              :label="$t('employees.employeeAgreements.departmentId')"
            ></va-select-input>
          </v-col>
          <v-col sm="3">
            <va-text-input
              v-show="false"
              :label="$t('employees.employeeAgreements.subDepartmentId')"
              v-model="model.subDepartmentName"
            ></va-text-input>
            <va-select-input
              v-if="item.departmentId != null"
              v-model="model.subDepartmentId"
              reference="subDepartments"
              :key="subDeparmantRenderKey"
              :filter="{ departmentId: item.departmentId }"
              @change="setSubDepartmentName($event)"
              :label="$t('employees.employeeAgreements.subDepartmentId')"
            ></va-select-input>
          </v-col>
          <v-col sm="3">
            <va-date-input
              v-model="model.startDate"
              format="short"
              :label="$t('employees.employeeAgreements.startDate')"
            ></va-date-input>
          </v-col>
          <v-col sm="3">
            <va-date-input
              v-model="model.endDate"
              format="short"
              :label="$t('employees.employeeAgreements.endDate')"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="3">
            <va-boolean-input
              v-model="model.isEnd"
              :label="$t('employees.employeeAgreements.isEnd')"
            ></va-boolean-input>
          </v-col>
          <v-col sm="3">
            <va-date-input
              v-model="model.earlyEndDate"
              format="short"
              :label="$t('employees.employeeAgreements.earlyEndDate')"
            ></va-date-input>
          </v-col>
          <v-col sm="6">
            <va-text-input
              v-model="model.endReason"
              :label="$t('employees.employeeAgreements.endReason')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="3">
            <va-select-input
              source="currencyId"
              v-model="model.currencyId"
              @change="detectCurrencyInput"
              :label="$t('employees.employeeAgreements.currencyId')"
            ></va-select-input>
          </v-col>
          <v-col sm="3">
            <va-currency-input
              v-model="model.grossSalary"
              :label="$t('employees.employeeAgreements.grossSalary')"
              :key="currencyId + '_grossSalary'"
              :options="{ currency: currencyId, precision: 2 }"
            ></va-currency-input>
          </v-col>
          <v-col sm="3">
            <va-currency-input
              v-model="model.netSalary"
              :label="$t('employees.employeeAgreements.netSalary')"
              :key="currencyId + '_netSalary'"
              :options="{ currency: currencyId, precision: 2 }"
            ></va-currency-input>
          </v-col>
          <v-col sm="3">
            <va-currency-input
              v-model="model.bonus"
              :label="$t('employees.employeeAgreements.bonus')"
              :key="currencyId + '_bonus'"
              :options="{ currency: currencyId, precision: 2 }"
            ></va-currency-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="3">
            <va-boolean-input
              v-model="model.oss"
              :label="$t('employees.employeeAgreements.oss')"
            ></va-boolean-input>
          </v-col>
          <v-col sm="3">
            <va-boolean-input
              v-model="model.car"
              :label="$t('employees.employeeAgreements.car')"
            ></va-boolean-input>
          </v-col>
          <v-col sm="3">
            <va-boolean-input
              v-model="model.ticket"
              :label="$t('employees.employeeAgreements.ticket')"
            ></va-boolean-input>
          </v-col>
          <v-col sm="3">
            <va-boolean-input
              v-model="model.transportation"
              :label="$t('employees.employeeAgreements.transportation')"
            ></va-boolean-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="6">
            <va-text-input
              v-model="model.description"
              :label="$t('employees.employeeAgreements.description')"
              multiline
            ></va-text-input>
          </v-col>
          <v-col sm="6"> </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-btn color="primary" dark class="mb-2" @click="save">
              {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import {
  required,
  // email,
  // minLength,
  // maxLength,
} from "vuelidate/lib/validators";

export default {
  props: ["edit", "customerId", "customerName"],
  data() {
    return {
      subDeparmantRenderKey: 0,
      currencyId: null,
      model: {
        agreementId: null,
        customerId: this.customerId,
        customerName: this.customerName,
        sowId: null,
        customerEmployeeNo: null,
        departmentId: null,
        departmentName: null,
        subDepartmentId: null,
        subDepartmentName: null,
        agreementTypeId: null,
        agreementTypeName: null,
        jobTitleId: null,
        jobTitleName: null,
        startDate: null,
        endDate: null,
        isEnd: null,
        endReason: null,
        earlyEndDate: null,
        currencyId: null,
        grossSalary: null,
        netSalary: null,
        description: null,
        bonus: null,
        oss: null,
        car: null,
        ticket: null,
        transportation: null,
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["employees/getDialogItem"];
      },
      set(model) {
        this.$store.commit(`employees/setDialogItem`, Object.assign({}, model));
      },
    },
    sowIdErrors() {
      const errors = [];
      const field = "sowId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    agreementTypeIdErrors() {
      const errors = [];
      const field = "agreementTypeId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
    this.currencyId = this.item.currencyId ? this.item.currencyId : "TRY";
  },
  validations: {
    model: {
      sowId: {
        required,
      },
      jobTitleId: {
        required,
      },
      agreementTypeId: {
        required,
      },
    },
  },
  methods: {
    renderSubDepartment() {
      this.subDeparmantRenderKey = this.subDeparmantRenderKey + 1;
    },
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.model.currencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.model.currencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    setDepartmentName(event) {
      this.model.departmentName = event.name;
      this.renderSubDepartment();
    },
    setSubDepartmentName(event) {
      this.model.subDepartmentName = event.name;
    },
    setAgreementTypeName(event) {
      this.model.agreementTypeName = event.name;
    },
    setJobTitleName(event) {
      this.model.jobTitleName = event.name;
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
