<template>
  <div>
    <v-row>
      <v-col sm="6" md="6">
        <va-text-input
          v-show="false"
          :label="$t('orders.itemDepartmentName')"
          source="model.itemDepartmentName"
          v-model="model.itemDepartmentName"
        ></va-text-input>
        <va-select-input
          v-model="model.itemDepartmentId"
          :filter="{
            customerId: model.itemCustomerId,
          }"
          :label="$t('orders.itemDepartmentId')"
          @change="setDepartmentName($event)"
          reference="departments"
          :error-messages="itemDepartmentIdErrors"
        ></va-select-input>
      </v-col>
      <v-col sm="6" md="6">
        <va-text-input
          v-show="false"
          :label="$t('orders.itemCurrencyName')"
          source="model.itemCurrencyName"
          v-model="model.itemCurrencyName"
        ></va-text-input>
        <va-select-input
          source="currencyId"
          v-model="model.itemCurrencyId"
          :label="$t('orders.itemCurrencyId')"
          @change="setCurrencyName($event)"
          :error-messages="itemCurrencyIdErrors"
        ></va-select-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="12" md="12">
        <va-text-input
          v-model="model.itemDescription"
          :label="$t('orders.itemDescription')"
          multiline
        ></va-text-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-currency-input
          v-model="model.itemExchangeRate"
          :options="{ currency: 'TRY', precision: 4 }"
          :label="$t('orders.itemExchangeRate')"
        ></va-currency-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-currency-input
          v-model="model.itemTaxAmount"
          :key="model.itemCurrencyId + '_itemTaxAmount'"
          :options="{ currency: currencyId, precision: 2 }"
          :label="$t('orders.itemTaxAmount')"
        ></va-currency-input>
      </v-col>
      <v-col sm="6" md="6">
        <va-currency-input
          v-model="model.itemTotalTaxAmount"
          :key="model.itemCurrencyId + '_itemTotalTaxAmount'"
          :options="{ currency: currencyId, precision: 2 }"
          :label="$t('orders.itemTotalTaxAmount')"
        ></va-currency-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-currency-input
          v-model="model.itemAmount"
          :key="model.itemCurrencyId + '_itemAmount'"
          :options="{ currency: currencyId, precision: 2 }"
          :label="$t('orders.itemAmount')"
        ></va-currency-input>
      </v-col>
      <v-col sm="6" md="6">
        <va-currency-input
          v-model="model.itemTotalAmount"
          :key="model.itemCurrencyId + '_itemTotalAmount'"
          :options="{ currency: currencyId, precision: 2 }"
          :label="$t('orders.itemTotalAmount')"
        ></va-currency-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-text-input
          v-show="false"
          source="model.purchaseOrderItemId"
          :label="$t('orders.purchaseOrderItemId')"
          v-model="model.purchaseOrderItemId"
        ></va-text-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="12">
        <v-btn color="primary" dark class="mb-2" @click="save">
          {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: ["edit", "customerId"],
  data() {
    return {
      currencyId: null,
      model: {
        purchaseOrderItemId: null,
        itemCustomerId: this.customerId,
        itemDepartment: null,
        itemDepartmentId: "",
        itemDepartmentName: "",
        itemCurrency: null,
        itemCurrencyName: "",
        itemCurrencyId: "",
        itemExchangeRate: "0",
        itemAmount: "0",
        itemTaxAmount: "0",
        itemTotalAmount: "0",
        itemTotalTaxAmount: "0",
        itemDescription: null,
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["orders/getDialogItem"];
      },
      set(model) {
        this.$store.commit(`orders/setDialogItem`, Object.assign({}, model));
      },
    },
    itemCurrencyIdErrors() {
      const errors = [];
      const field = "itemCurrencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemDepartmentIdErrors() {
      const errors = [];
      const field = "itemDepartmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
    this.currencyId = this.item.itemCurrencyId
      ? this.item.itemCurrencyId
      : "TRY";
  },
  validations: {
    model: {
      itemCurrencyId: {
        required,
      },
      itemDepartmentId: {
        required,
      },
    },
  },
  methods: {
    detectCurrencyInput() {
      if (this.model.itemCurrencyId) {
        this.currencyId = this.model.itemCurrencyId;
      }
    },
    setDepartmentName(event) {
      this.model.itemDepartmentName = event.name;
      this.model.itemDepartmentId = event.id;
    },
    setCurrencyName(event) {
      this.model.itemCurrencyName = event.name;
      this.model.itemCurrencyId = event.id;
      this.detectCurrencyInput();
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
