<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import { required, maxLength, numeric } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      tabs: null,
      menu: false,
      model: {
        id: null,
        customerColor: "#FFFFFF",
        customerShortName: null,
        customerName: null,
        address1: null,
        address2: null,
        zipCode: null,
        countryId: null,
        cityId: null,
        taxOffice: null,
        taxNumber: null,
        customerJobTitles: null,
        customerAllowances: null,
        customerExpenseTypes: null,
      },
      customerJobTitles: [],
      customerAllowances: [],
      customerExpenseTypes: [],
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: this.$t("customers.jobTitleName"),
          value: "jobTitleName",
        },
        {
          text: this.$t("customers.jobTitleActions"),
          value: "actions",
          sortable: false,
        },
      ],
      editedIndex: -1,
      editedItem: {
        jobTitleId: "",
        jobTitleName: "",
      },
      defaultItem: {
        jobTitleId: "",
        jobTitleName: "",
      },
      allowanceDialog: false,
      allowanceDialogDelete: false,
      headerAllowances: [
        {
          text: this.$t("customers.allowanceName"),
          value: "allowanceName",
        },
        {
          text: this.$t("customers.allowanceActions"),
          value: "actions",
          sortable: false,
        },
      ],
      allowanceEditedIndex: -1,
      allowanceEditedItem: {
        allowanceId: "",
        allowanceName: "",
      },
      allowanceDefaultItem: {
        allowanceId: "",
        allowanceName: "",
      },
      expenseTypeDialog: false,
      expenseTypeDialogDelete: false,
      headerExpenseTypes: [
        {
          text: this.$t("customers.expenseTypeName"),
          value: "expenseTypeName",
        },
        {
          text: this.$t("customers.expenseTypeActions"),
          value: "actions",
          sortable: false,
        },
      ],
      expenseTypeEditedIndex: -1,
      expenseTypeEditedItem: {
        expenseTypeId: "",
        expenseTypeName: "",
      },
      expenseTypeDefaultItem: {
        expenseTypeId: "",
        expenseTypeName: "",
      },
    };
  },
  validations: {
    model: {
      customerShortName: {
        required,
        maxLength: maxLength(100),
      },
      customerColor: {
        required,
        maxLength: maxLength(7),
      },
      customerName: {
        required,
        maxLength: maxLength(255),
      },
      address1: {
        maxLength: maxLength(255),
      },
      address2: {
        maxLength: maxLength(255),
      },
      zipCode: {
        numeric,
        maxLength: maxLength(10),
      },
      countryId: {
        required,
        maxLength: maxLength(2),
      },
      cityId: {
        required,
        numeric,
      },
      taxOffice: {
        maxLength: maxLength(100),
      },
      taxNumber: {
        numeric,
        maxLength: maxLength(20),
      },
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    allowanceFormTitle() {
      return this.allowanceEditedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    expenseTypeFormTitle() {
      return this.expenseTypeEditedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    customerShortNameErrors() {
      const errors = [];
      const field = "customerShortName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerColorErrors() {
      const errors = [];
      const field = "customerColor";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "7" }));
      return errors;
    },
    customerNameErrors() {
      const errors = [];
      const field = "customerName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    zipCodeErrors() {
      const errors = [];
      const field = "zipCode";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "10" }));
      return errors;
    },
    countryIdErrors() {
      const errors = [];
      const field = "countryId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "2" }));
      return errors;
    },
    cityIdErrors() {
      const errors = [];
      const field = "cityId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      return errors;
    },
    taxOfficeErrors() {
      const errors = [];
      const field = "taxOffice";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    taxNumberErrors() {
      const errors = [];
      const field = "taxNumber";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].numeric &&
        errors.push(this.$t("v.number.numeric"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "20" }));
      return errors;
    },
  },
  created() {
    this.initializeCustomerJobTitles();
    this.initializeCustomerAllowances();
    this.initializeCustomerExpenseTypes();
    this.model.id = generateId(this);
    if (this.item == null) {
      this.model.countryId = "TR";
    }
  },
  methods: {
    initializeCustomerJobTitles() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerJobTitles")
      ) {
        this.model.customerJobTitles = this.item.customerJobTitles;
        this.customerJobTitles = JSON.parse(this.item.customerJobTitles);
      }
    },
    initializeCustomerAllowances() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerAllowances")
      ) {
        this.model.customerAllowances = this.item.customerAllowances;
        this.customerAllowances = JSON.parse(this.item.customerAllowances);
      }
    },
    initializeCustomerExpenseTypes() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerExpenseTypes")
      ) {
        this.model.customerExpenseTypes = this.item.customerExpenseTypes;
        this.customerExpenseTypes = JSON.parse(this.item.customerExpenseTypes);
      }
    },
    setJobTitleName(event) {
      this.editedItem.jobTitleName = event.name;
    },
    setExpenseTypeName(event) {
      this.expenseTypeEditedItem.expenseTypeName = event.name;
    },
    setAllowanceName(event) {
      this.allowanceEditedItem.allowanceName = event.name;
    },
    editItem(item) {
      this.editedIndex = this.customerJobTitles.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.model.customerJobTitles = JSON.stringify(this.customerJobTitles);
      this.dialog = true;
    },
    deleteItem(item) {
      this.editedIndex = this.customerJobTitles.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.customerJobTitles.splice(this.editedIndex, 1);
      this.model.employeChildren = JSON.stringify(this.customerJobTitles);
      this.closeDelete();
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(
          this.customerJobTitles[this.editedIndex],
          this.editedItem
        );
      } else {
        this.customerJobTitles.push(this.editedItem);
      }
      this.model.customerJobTitles = JSON.stringify(this.customerJobTitles);
      this.close();
    },
    allowanceEditItem(item) {
      this.allowanceEditedIndex = this.customerAllowances.indexOf(item);
      this.allowanceEditedItem = Object.assign({}, item);
      this.model.customerAllowances = JSON.stringify(this.customerAllowances);
      this.allowanceDialog = true;
    },
    allowanceDeleteItem(item) {
      this.allowanceEditedIndex = this.customerAllowances.indexOf(item);
      this.allowanceEditedItem = Object.assign({}, item);
      this.allowanceDialogDelete = true;
    },
    allowanceDeleteItemConfirm() {
      this.customerAllowances.splice(this.allowanceEditedIndex, 1);
      this.model.customerAllowances = JSON.stringify(this.customerAllowances);
      this.allowanceCloseDelete();
    },
    allowanceClose() {
      this.allowanceDialog = false;
      this.$nextTick(() => {
        this.allowanceEditedItem = Object.assign({}, this.allowanceDefaultItem);
        this.allowanceEditedIndex = -1;
      });
    },
    allowanceCloseDelete() {
      this.allowanceDialogDelete = false;
      this.$nextTick(() => {
        this.allowanceEditedItem = Object.assign({}, this.allowanceDefaultItem);
        this.allowanceEditedIndex = -1;
      });
    },
    allowanceSave() {
      if (this.allowanceEditedIndex > -1) {
        Object.assign(
          this.customerAllowances[this.allowanceEditedIndex],
          this.allowanceEditedItem
        );
      } else {
        this.customerAllowances.push(this.allowanceEditedItem);
      }
      this.model.customerAllowances = JSON.stringify(this.customerAllowances);
      this.allowanceClose();
    },
    expenseTypeEditItem(item) {
      this.expenseTypeEditedIndex = this.customerExpenseTypes.indexOf(item);
      this.expenseTypeEditedItem = Object.assign({}, item);
      this.model.customerExpenseTypes = JSON.stringify(
        this.customerExpenseTypes
      );
      this.expenseTypeDialog = true;
    },
    expenseTypeDeleteItem(item) {
      this.expenseTypeEditedIndex = this.customerExpenseTypes.indexOf(item);
      this.expenseTypeEditedItem = Object.assign({}, item);
      this.expenseTypeDialogDelete = true;
    },
    expenseTypeDeleteItemConfirm() {
      this.customerExpenseTypes.splice(this.expenseTypeEditedIndex, 1);
      this.model.customerExpenseTypes = JSON.stringify(
        this.customerExpenseTypes
      );
      this.expenseTypeCloseDelete();
    },
    expenseTypeClose() {
      this.expenseTypeDialog = false;
      this.$nextTick(() => {
        this.expenseTypeEditedItem = Object.assign(
          {},
          this.expenseTypeDefaultItem
        );
        this.expenseTypeEditedIndex = -1;
      });
    },
    expenseTypeCloseDelete() {
      this.expenseTypeDialogDelete = false;
      this.$nextTick(() => {
        this.expenseTypeEditedItem = Object.assign(
          {},
          this.expenseTypeDefaultItem
        );
        this.expenseTypeEditedIndex = -1;
      });
    },
    expenseTypeSave() {
      if (this.expenseTypeEditedIndex > -1) {
        Object.assign(
          this.customerExpenseTypes[this.expenseTypeEditedIndex],
          this.expenseTypeEditedItem
        );
      } else {
        this.customerExpenseTypes.push(this.expenseTypeEditedItem);
      }
      this.model.customerExpenseTypes = JSON.stringify(
        this.customerExpenseTypes
