                          <td>{{ item.tckn }}</td>
                          <td>{{ item.pernetNumber }}</td>
                      </tr>
                  </template>
                  </v-data-table>
                  <v-row class="text-right">
                    <v-col>
                      <v-btn class="mb-2" v-if="employees.length > 0 && selectedEmployees.length > 0" @click="addToSowEmployees">
