                      v-model="model.purchaseOrders"
                    ></va-text-input>
                    <template>
                      <v-data-table
                        :headers="headerOrders"
                        :items="purchaseOrders"
                        sort-by="name"
                        class="elevation-1"
                        hide-default-footer
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-spacer></v-spacer>

                            <v-dialog v-model="dialog" max-width="1000px">
                              <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                  color="primary"
                                  dark
                                  class="mb-2"
                                  v-bind="attrs"
                                  v-on="on"
                                >
                                  {{ $t("orders.purchaseOrderButton") }}
                                </v-btn>
                              </template>

                              <v-card>
                                <v-card-text>
                                  <v-container>
                                    <template v-if="showError">{{
                                      errorMessage
                                    }}</template>
                                    <template v-else>
                                      <va-list
                                        resource="orders"
                                        :filters="filters"
                                        :filter="{
                                          customerId: model.customerId,
                                        }"
                                        :items-per-page="50"
                                        :disableExport="true"
                                        :disable-items-per-page="false"
                                        :disable-global-search="true"
                                        :disablePagination="true"
                                        :hideBulkDelete="true"
                                        @selected="onSelect"
                                      >
                                        <va-data-table
                                          :disableActions="true"
                                          :fields="fields"
                                        ></va-data-table>
                                      </va-list>
                                      <!--                               <v-data-table
                                    v-model="selected"
                                    :headers="headerPopupOrders"
                                    :items="popupOrders"
                                    item-key="purchaseOrderId"
                                    show-select
                                  >
                                  </v-data-table> -->
                                    </template>
                                  </v-container>
                                </v-card-text>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="close"
                                  >
                                    {{ $t("dialog.cancelButton") }}
                                  </v-btn>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="save"
                                  >
                                    {{ $t("dialog.saveButton") }}
                                  </v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>

                            <v-dialog v-model="dialogDelete" max-width="500px">
                              <v-card>
                                <v-card-title class="text-h5">{{
                                  $t("dialog.deleteConfirm")
                                }}</v-card-title>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="closeDelete"
                                    >{{ $t("dialog.closeDeleteButton") }}</v-btn
                                  >
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="deleteItemConfirm"
                                    >{{
                                      $t("dialog.confirmDeleteButton")
                                    }}</v-btn
                                  >
                                  <v-spacer></v-spacer>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.actions="{ item }">
                          <v-icon small @click="deleteItem(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                        <!-- <template v-slot:no-data></template> -->

                        <!--                   <template v-slot:[`field.itemDepartmentId`]="{ editedItem }">
                    <span>{{ new Date(item.calculationDate).toLocaleDateString("tr") }}</span>
                  </template> -->
                      </v-data-table>
                    </template>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </va-form>
  </div>
</template>

<script>
// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components
//
import { generateId, findTotalMonths, arrayGroupBy } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      sowEmployees: [],
      parentChecked: [],
      selectedSelected: [],
      selectedEmployees: [],
      markedEmployees: [],
      groupedEmployees: [],
      employees: [],
      selected: [],
      purchaseOrders: [],
      search: '',
      loadingEmployees: false,
      itemLocal: this.item,
      filters: [
        { source: "purchaseOrderNo", label: this.$t("sows.purchaseOrderNo") },
        { source: "customerOrderNo", label: this.$t("sows.customerOrderNo") },
        {
          source: "startDateStart",
          type: "date",
          label: this.$t("orders.startDateStart"),
        },
        {
          source: "startDateEnd",
          type: "date",
          label: this.$t("orders.startDateEnd"),
        },
        {
          source: "endDateStart",
          type: "date",
          label: this.$t("orders.endDateStart"),
        },
        {
          source: "endDateEnd",
          type: "date",
          label: this.$t("orders.endDateEnd"),
        },
      ],
      fields: [
        {
          source: "customerShortName",
          label: this.$t("sows.customerShortName"),
        },
        { source: "purchaseOrderNo", label: this.$t("sows.purchaseOrderNo") },
        { source: "customerOrderNo", label: this.$t("sows.customerOrderNo") },
        { source: "startDate", label: this.$t("sows.startDate") },
        { source: "endDate", label: this.$t("sows.endDate") },
      ],
      sowEmployeeFields: [
        {
          source: "customerShortName",
          label: this.$t("employees.customerShortName"),
          sortable: true,
        },
        {
          source: "name",
          label: this.$t("employees.name"),
          sortable: true,
        }
      ],
      tabs: null,
      model: {
        id: null,
        customerId: "",
        departmentId: "",
        purchaserId: "",
        sowName: null,
        sowDescription: null,
        startDate: null,
        endDate: null,
        currencyId: "",
        amount: "0",
        fiftyFiveTen: false,
        incentives: false,
        startYearId: "",
        endYearId: "",
        startMonthId: "",
        endMonthId: "",
        totalMonth: null,
        grConsent: false,
        flexyForce: false,
        comissionRate: "0",
        serviceCostPerPerson: "0",
        ehsAmount: "0",
        totalAmount: "0",
        purchaseOrders: "",
        sowEmployees: ""
      },
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 2,
        masked: false,
      },
      rate: {
        decimal: "",
        thousands: "",
        prefix: "",
        suffix: " %",
        precision: 0,
        masked: false,
      },
      dialog: false,
      dialogDelete: false,
      headerOrders: [
        { text: this.$t("sows.customerShortName"), value: "customerShortName" },
        { text: this.$t("sows.purchaseOrderNo"), value: "purchaseOrderNo" },
        { text: this.$t("sows.customerOrderNo"), value: "customerOrderNo" },
        { text: this.$t("sows.startDate"), value: "startDate" },
        { text: this.$t("sows.endDate"), value: "endDate" },
        { text: this.$t("sows.orderActions"), value: "actions" },
      ],
      editedIndex: -1,
      editedItem: {
        sowId: "",
        customerShortName: "",
        purchaseOrderId: "",
        customerOrderNo: "",
        purchaseOrderNo: "",
        startDate: null,
        endDate: null,
      },
      defaultItem: {
        sowId: "",
        customerShortName: "",
        purchaseOrderId: "",
        customerOrderNo: "",
        purchaseOrderNo: "",
        startDate: null,
        endDate: null,
      },
      defaultEmployeeItem: {
        id: null,
        employeeId: null,
        customerShortName: "",
        tckn: null,
        pernetNumber: null,
      },
      showError: false,
      errorMessage: null,
    };
  },
  validations: {
    model: {
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      departmentId: {
        required,
        maxLength: maxLength(36),
      },
      subDepartmentId: {
        maxLength: maxLength(36),
      },
      purchaserId: {
        required,
        maxLength: maxLength(36),
      },
      sowName: {
        required,
        maxLength: maxLength(100),
      },
      sowDescription: {
        required,
        maxLength: maxLength(255),
      },
      startDate: {
        required,
      },
      endDate: {
        required,
      },
      currencyId: {
        required,
        maxLength: maxLength(3),
      },
    },
  },
  watch: {
    dialog(val) {
      if (val == true) {
        // dialog open
        // this.editedItem.itemCustomerId = this.customerId
        this.detectCurrencyInput();
      }
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    headers() {
      return  [
        {
          value: "groupName",
          sortable: false,
        },
        {
          text: this.$t("employees.customerShortName"),
          value: "customerShortName",
          sortable: false,
        },
        {
          text: this.$t("employees.name"),
          value: "name",
          sortable: false,
        },
        {
          value: "tckn",
          text: this.$t("employees.tckn"),
          sortable: false,
        },
        {
          value: "pernetNumber",
          text: this.$t("employees.pernetNumber"),
          sortable: false,
        },
      ]
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    departmentIdErrors() {
      const errors = [];
      const field = "departmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    subDepartmentIdErrors() {
      const errors = [];
      const field = "subDepartmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    purchaserIdErrors() {
      const errors = [];
      const field = "purchaserId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    sowNameErrors() {
      const errors = [];
      const field = "sowName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    sowDescriptionErrors() {
      const errors = [];
      const field = "sowDescription";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
    startDateErrors() {
      const errors = [];
      const field = "startDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    endDateErrors() {
      const errors = [];
      const field = "endDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    currencyIdErrors() {
      const errors = [];
      const field = "currencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "3" }));
      return errors;
    },
  },
  created() {
    this.initializePurchaseOrders();
    this.initializeSowEmployees();
    this.model.id = generateId(this)
    if (!this.model.currencyId) {
      this.model.currencyId = "TRY";
    }
  },
  mounted() {
    if (this.item == null) {
      return;
    }
    if (this.item.currencyId == "TRY") {
      this.money.suffix = " TL";
    }
    if (this.item.currencyId == "EUR") {
      this.money.suffix = " EUR";
    }
    if (this.item.currencyId == "USD") {
      this.money.suffix = " USD";
    }
  },
  methods: {
    clearSearchData() {
      this.employees = []
    },
    // enterSelect() {
    //   console.error(this.selectedSelected)
    // },
    deleteSelectedItems() {
      let Self = this
      if (Array.isArray(this.selectedSelected) && this.selectedSelected.length > 0) {
        this.selectedSelected.forEach(function (val, i) {
          Self.sowEmployees = Self.sowEmployees.filter(function (ele) {
            return ele.id !== val.id;
          });
        });
      }
      Self.model.sowEmployees = JSON.stringify(Self.sowEmployees);
    },
    async searchDb() {
      await this.searchSowEmployees(this.search)
    },
    initializeSowEmployees() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "sowEmployees")
      ) {
        this.sowEmployees = JSON.parse(this.item.sowEmployees);
        this.model.sowEmployees = this.item.sowEmployees;
      }
    },
    initializePurchaseOrders() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "purchaseOrders")
      ) {
        this.purchaseOrders = JSON.parse(this.item.purchaseOrders);
        this.model.purchaseOrders = this.item.purchaseOrders;
      }
    },
    async searchSowEmployees(q) {
      this.loadingEmployees = true
      let url = "/employees/findAllBySearch"
      if (q) {
        url = "/employees/findAllBySearch?q=" + q
      }
      let response = await this.$admin.http.get(url);
      this.employees = response.data.data;
      this.groupedEmployees = arrayGroupBy(this.employees, 'customerShortName')
      this.loadingEmployees = false
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "sowEmployees")
      ) {
        this.selectedEmployees = JSON.parse(this.item.sowEmployees);
        // this.model.sowEmployees = this.item.sowEmployees
        this.initializeParentCheckboxes()
      }
    },
    initializeParentCheckboxes() {
        let selectedGroupEmployees = arrayGroupBy(this.selectedEmployees, 'customerShortName')
        let Self = this
        Object.keys(this.groupedEmployees).forEach(function (group) {
            let childrenCount = Self.groupedEmployees[group].length
            if (typeof(selectedGroupEmployees[group]) != 'undefined' 
              && selectedGroupEmployees[group].length == childrenCount) {
                Self.parentChecked[group] = true
            }
        })
    },
    filterText (value, search) {
      return value != null &&
        search != null &&
        typeof value === 'string' &&
        value.toString().toLocaleLowerCase().indexOf(search) !== -1
    },
    toggle(isSelected,select,e,group) {
      select(!isSelected)
      let selectedChildrenCount = this.groupedEmployees[group].length
      let selectedGroupCount = 1
      this.selectedEmployees.forEach(function (val) {
        if (val.customerShortName == group) {
            if (isSelected) {
              --selectedGroupCount  
            } else {
              ++selectedGroupCount  
            }
        }
      })
      if (selectedGroupCount == selectedChildrenCount) {
        this.parentChecked[group] = true
      } else {
        this.parentChecked[group] = false
      }
      // let Self = this
      // setTimeout(function(){
      //   Self.model.sowEmployees = JSON.stringify(Self.selectedEmployees)
      // }, 1000);
    },
    toggleChildren(group, e) {
      const Self = this;
        if (e) { // mark child checkboxes
          let selectedIds = []
          this.selectedEmployees.forEach(function (val) {
              selectedIds.push(val.permId)
          })
          if (typeof this.groupedEmployees[group] != 'undefined') {
              this.groupedEmployees[group].forEach(function (gVal) {
                if (!selectedIds.includes(gVal.permId)) {
                  Self.selectedEmployees.push(gVal)
                }
              })
              // toggleParent()
          }
        } else { // unmark child checkboxes
          if (typeof this.groupedEmployees[group] != 'undefined') {
            this.groupedEmployees[group].forEach(function (gVal) {
                Self.selectedEmployees = Self.selectedEmployees.filter(item => item.employeeId != gVal.employeeId)
            })
            // toggleParent()
          }
        }
        // this.model.sowEmployees = JSON.stringify(this.selectedEmployees)
    },
    addToSowEmployees() {
      let Self = this
      if (Array.isArray(this.selectedEmployees) && this.selectedEmployees.length > 0) {
        this.selectedEmployees.forEach(function (val) {
          let doesExist = Self.sowEmployees.some(function (ele) {
            return ele.id === val.id;
          });
          // do not add same record two times
          //
          if (!doesExist) {
            Self.sowEmployees.push(val);
          }
        });
      }
      this.model.sowEmployees = JSON.stringify(this.sowEmployees)
    },
    detectCurrencyInput() {
      if (this.model.currencyId == "TRY") {
        this.money.suffix = " TL";
      }
      if (this.model.currencyId == "EUR") {
        this.money.suffix = " EUR";
      }
      if (this.model.currencyId == "USD") {
        this.money.suffix = " USD";
      }
    },
    detectTotalMonths() {
      if (
        this.model.startYearId &&
        this.model.startMonthId &&
        this.model.endYearId &&
        this.model.endMonthId
      ) {
        let startDate =
          String(this.model.startYearId) +
          "-" +
          String(this.model.startMonthId) +
          "-01";
        let endDate =
          String(this.model.endYearId) +
          "-" +
          String(this.model.endMonthId) +
          "-01";
        let totalMonth = findTotalMonths(startDate, endDate);
        this.model.totalMonth = String(totalMonth);
      }
    },
    deleteItem(item) {
      this.editedIndex = this.purchaseOrders.indexOf(item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.purchaseOrders.splice(this.editedIndex, 1);
      this.model.purchaseOrders = JSON.stringify(this.purchaseOrders);
      this.closeDelete();
    },
    close() {
      this.dialog = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    onValidate() {
      if (this.$v.$invalid) {
        this.tabs = 0;
      }
    },
    onSelect(selected) {
      this.selected = selected;
    },
    save() {
      let sowId = this.id ? this.id : this.uid;
      let Self = this;
      this.selected.forEach(function (val) {
        var doesExist = Self.purchaseOrders.some(function (ele) {
          return ele.purchaseOrderId === val.purchaseOrderId;
        });
        // do not add same record two times
        //
        if (!doesExist) {
          Self.purchaseOrders.push({
            sowId: sowId,
            customerShortName: val.customerShortName,
            purchaseOrderId: val.purchaseOrderId,
            customerOrderNo: val.customerOrderNo,
            purchaseOrderNo: val.purchaseOrderNo,
            startDate: val.startDate,
            endDate: val.endDate,
          });
        }
      });
      this.model.purchaseOrders = JSON.stringify(this.purchaseOrders);
      this.close();
    },
  },
};
</script>
