<template>
  <v-app>
    <v-container
      fluid
      fill-height
      id="login-page"
      :style="{ backgroundColor: themeColor }"
    >
      <v-layout align-center justify-center>
        <v-flex :style="{ 'max-width': '350px' }">
          <v-card>
            <v-card-text>
              <div class="text-center mb-4">
                <img src="../assets/logo-gray.png" width="80" height="80" />
              </div>
              <transition name="fade" mode="out-in">
                <router-view></router-view>
              </transition>
              <v-row>
                <v-col>
                  <v-menu v-model="languageMenu" offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn class="text-capitalize" v-bind="attrs" v-on="on" text>
                        <v-icon left>mdi-translate</v-icon>
                        {{ activeLang }}
                        <v-icon small right>mdi-menu-down</v-icon>
                      </v-btn>
                    </template>
                    <v-list dense>
                      <v-list-item
                        v-for="(lang, index) in langs"
                        :key="index"
                        @click="setLocale(lang)"
                      >
                        <v-list-item-title>{{ lang.title }}</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu>
                </v-col>
              </v-row>

            </v-card-text>
          </v-card>

        <v-row class="pt-5">
          <v-col sm="12">
            <div class="text-center">
              <router-link :to="{ name: 'forgotPassword' }" style="color:white;font-size:13px">
                {{ $t("auth.forgotPassword") }}
              </router-link>
            </div>
          </v-col>
        </v-row>

        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>
