<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-row>
          <v-col sm="12">
            <va-text-input
              v-show="false"
              v-model="model.allowanceId"
              :label="$t('costs.allowanceId')"
            ></va-text-input>
            <va-text-input
              v-show="false"
              v-model="model.allowanceName"
              :label="$t('costs.allowanceName')"
            ></va-text-input>
          </v-col>
          <v-col sm="12">
            <va-select-input
              source="currencyId"
              v-model="model.allowanceCurrencyId"
              @change="detectCurrencyInput"
              :error-messages="allowanceCurrencyIdErrors"
              :label="$t('costs.allowanceCurrencyId')"
            ></va-select-input>
          </v-col>
          <v-col sm="12">
            <va-currency-input
              v-model="model.allowanceAmount"
              :key="currencyId + '_allowanceAmount'"
              :error-messages="allowanceAmountErrors"
              :options="{ currency: currencyId, precision: 2 }"
              :label="$t('costs.allowanceAmount')"
            ></va-currency-input>
          </v-col>
          <v-col sm="12">
            <va-select-input
              source="salaryTypeId"
              v-model="model.allowanceSalaryTypeId"
              :error-messages="allowanceSalaryTypeIdErrors"
              :label="$t('costs.allowanceSalaryTypeId')"
            ></va-select-input>
          </v-col>
          <v-col sm="12">
            <va-boolean-input
              v-model="model.allowanceExceptPayroll"
              :label="$t('costs.allowanceExceptPayroll')"
            ></va-boolean-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-btn color="primary" dark class="mb-2" @click="save">
              {{ $t("dialog.saveButton") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import {
  required,
  // email,
  // minLength,
  // maxLength,
} from "vuelidate/lib/validators";

export default {
  props: ["allowanceId", "allowanceName"],
  data() {
    return {
      currencyId: null,
      model: {
        allowanceId: this.allowanceId,
        allowanceName: this.allowanceName,
        allowanceAmount: "",
        allowanceCurrencyId: "",
        allowanceSalaryTypeId: "",
        allowanceExceptPayroll: false,
      },
    };
  },
  computed: {
    item: {
      get() {
        return this.$store.getters["costs/getDialogItem"];
      },
      set(model) {
        this.$store.commit(`costs/setDialogItem`, Object.assign({}, model));
      },
    },
    allowanceCurrencyIdErrors() {
      const errors = [];
      const field = "allowanceCurrencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    allowanceAmountErrors() {
      const errors = [];
      const field = "allowanceAmount";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    allowanceSalaryTypeIdErrors() {
      const errors = [];
      const field = "allowanceSalaryTypeId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
    this.currencyId = this.item.allowanceCurrencyId
      ? this.item.allowanceCurrencyId
      : "TRY";
  },
  validations: {
    model: {
      allowanceCurrencyId: {
        required,
      },
      allowanceAmount: {
        required,
      },
      allowanceSalaryTypeId: {
        required,
      },
    },
  },
  methods: {
    detectCurrencyInput() {
      if (this.model.allowanceCurrencyId == "TRY") {
        this.currencyId = "TRY";
      }
      if (this.model.allowanceCurrencyId == "EUR") {
        this.currencyId = "EUR";
      }
      if (this.model.allowanceCurrencyId == "USD") {
        this.currencyId = "USD";
      }
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
