<template>
  <va-form :id="id" :item="item" v-model="model">
    <v-row justify="center">
      <v-col sm="12">
        <v-card>
          <v-card-text>
            <v-row>
              <v-col sm="12">
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      source="departmentName"
                      :error-messages="departmentNameErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-select-input
                      source="customerId"
                      model="customerId"
                      reference="customers"
                      :error-messages="customerIdErrors"
                    ></va-select-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      source="managerName"
                      :error-messages="managerNameErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="managerSurname"
                      :error-messages="managerSurnameErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="managerPhoneAreaCodeId"
                      reference="areaCodes"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="managerPhone"
                      :error-messages="managerPhoneErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="managerMobileAreaCodeId"
                      reference="areaCodes"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="managerMobile"
                      :error-messages="managerMobileErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="3">
                    <va-text-input
                      source="managerEmail"
                      :error-messages="managerEmailErrors"
                    ></va-text-input>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>

            <v-row>
              <v-col sm="12">
                <va-text-input
                  v-show="false"
                  source="subDepartments"
                  v-model="model.subDepartments"
                ></va-text-input>
                <template>
                  <v-data-table
                    :headers="headers"
                    :items="subDepartments"
                    sort-by="name"
                    hide-default-footer
                    class="elevation-1"
                  >
                    <template v-slot:top>
                      <v-toolbar flat>
                        <v-toolbar-title>{{
                          $t("departments.subDepartments")
                        }}</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <v-spacer></v-spacer>

                        <v-dialog v-model="dialog" max-width="800px">
                          <template v-slot:activator="{ attrs }">
                            <v-btn
                              color="primary"
                              dark
                              class="mb-2"
                              v-bind="attrs"
                              @click="open"
                            >
                              {{ $t("departments.subDepartmentButton") }}
                            </v-btn>
                          </template>
                          <v-card>
                            <v-card-title>
                              <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>
                            <v-card-text>
                              <v-container>
                                <departments-subdepartments
                                  :edit="subDepartmentsEditWindow"
                                  :key="renderKeySubDepartments"
                                  :departmentId="id"
                                  @save="subDepartmentsModalSave"
                                >
                                </departments-subdepartments>
                              </v-container>
                            </v-card-text>
                            <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn color="blue darken-1" text @click="close">
                                {{ $t("dialog.cancelButton") }}
                              </v-btn>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                        
                        <v-dialog v-model="dialogDelete" max-width="500px">
                          <v-card>
                            <v-card-title class="text-h5">{{
                              $t("dialog.deleteConfirm")
                            }}</v-card-title>
                            <v-card-actions>
                              <v-spacer></v-spacer>
                              <v-btn
                                color="blue darken-1"
                                text
                                @click="closeDelete"
                                >{{ $t("dialog.closeDeleteButton") }}</v-btn
                              >
                              <v-btn
                                color="blue darken-1"
                                text
                                @click="deleteItemConfirm"
                                >{{ $t("dialog.confirmDeleteButton") }}</v-btn
                              >
                              <v-spacer></v-spacer>
                            </v-card-actions>
                          </v-card>
                        </v-dialog>
                      </v-toolbar>
                    </template>
                    <template v-slot:item.actions="{ item }">
                      <v-icon small class="mr-2" @click="editItem(item)">
                        mdi-pencil
                      </v-icon>
                      <v-icon small @click="deleteItem(item)">
                        mdi-delete
                      </v-icon>
                    </template>
                    <!-- <template v-slot:no-data></template> -->
                  </v-data-table>
                </template>
              </v-col>
            </v-row>

            <va-save-button></va-save-button>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </va-form>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components

import { generateId } from "../../js/common";
import {
  required,
  minLength,
  maxLength,
  email,
} from "vuelidate/lib/validators";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      subDepartmentsEditWindow: false,
      renderKeySubDepartments: 1001,
      model: {
        id: null,
        departmentName: null,
        customerId: null,
        managerName: null,
        managerSurname: null,
        managerPhoneAreaCodeId: "TR",
        managerPhone: null,
        managerMobileAreaCodeId: "TR",
        managerMobile: null,
        managerEmail: null,
        subDepartments: "",
      },
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: this.$t("departments.subDepartmentName"),
          value: "subDepartmentName",
        },
        {
          text: this.$t("departments.subDepartmentManagerName"),
          value: "subDepartmentManagerName",
        },
        {
          text: this.$t("departments.subDepartmentManagerSurname"),
          value: "subDepartmentManagerSurname",
        },
        {
          text: this.$t("departments.subDepartmentManagerPhoneAreaCodeId"),
          value: "subDepartmentManagerPhoneAreaCodeId",
        },
        {
          text: this.$t("departments.subDepartmentManagerPhone"),
          value: "subDepartmentManagerPhone",
        },
        {
          text: this.$t("departments.subDepartmentManagerMobileAreaCodeId"),
          value: "subDepartmentManagerMobileAreaCodeId",
        },
        {
          text: this.$t("departments.subDepartmentManagerMobile"),
          value: "subDepartmentManagerMobile",
        },
        {
          text: this.$t("departments.subDepartmentManagerEmail"),
          value: "subDepartmentManagerEmail",
        },
        {
          text: this.$t("departments.subDepartmentActions"),
          value: "actions",
          sortable: false,
        },
      ],
      subDepartments: [],
      editedIndex: -1,
      editedItem: {
        subDepartmentId: "",
        subDepartmentName: "",
        subDepartmentManagerName: "",
        subDepartmentManagerSurname: "",
        subDepartmentManagerPhoneAreaCodeId: "",
        subDepartmentManagerPhone: "",
        subDepartmentManagerMobileAreaCodeId: "",
        subDepartmentManagerMobile: "",
        subDepartmentManagerEmail: "",
      },
      defaultItem: {
        subDepartmentId: "",
        subDepartmentName: "",
        subDepartmentManagerName: "",
        subDepartmentManagerSurname: "",
        subDepartmentManagerPhoneAreaCodeId: "",
        subDepartmentManagerPhone: "",
        subDepartmentManagerMobileAreaCodeId: "",
        subDepartmentManagerMobile: "",
        subDepartmentManagerEmail: "",
      },
    };
  },
  validations: {
    model: {
      departmentName: {
        required,
        maxLength: maxLength(100),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      managerName: {
        required,
        maxLength: maxLength(100),
      },
      managerSurname: {
        required,
        maxLength: maxLength(100),
      },
      managerPhone: {
        minLength: minLength(10),
      },
      managerMobile: {
        minLength: minLength(10),
      },
      managerEmail: {
        email,
      },
    },
  },
  watch: {
    dialog(val) {
      if (val == true) {
        // dialog open
        this.subDepartmentsRenderModalComponent();
      }
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    departmentNameErrors() {
      const errors = [];
      const field = "departmentName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    managerNameErrors() {
      const errors = [];
      const field = "managerName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    managerSurnameErrors() {
      const errors = [];
      const field = "managerSurname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    managerPhoneErrors() {
      const errors = [];
      const field = "managerPhone";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
    managerMobileErrors() {
      const errors = [];
      const field = "managerMobile";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "10" }));
      return errors;
    },
    managerEmailErrors() {
      const errors = [];
      const field = "managerEmail";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].email && errors.push(this.$t("v.email.invalid"));
      return errors;
    },
  },
  created() {
    this.initializeSubDepartments();
    this.model.id = generateId(this);
  },
  methods: {
    subDepartmentsRenderModalComponent() {
      this.renderKeySubDepartments = this.renderKeyItems + 1;
    },
    initializeSubDepartments() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "subDepartments")
      ) {
        this.subDepartments = JSON.parse(this.item.subDepartments);
        this.model.subDepartments = this.item.subDepartments;
      }
    },
    editItem(item) {
      this.subDepartmentsEditWindow = true;
      this.editedIndex = this.subDepartments.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
      this.$store.commit(
        `departments/setDialogItem`,
        Object.assign({}, this.editedItem)
      );
    },
    deleteItem(item) {
      this.editedIndex = this.subDepartments.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.subDepartments.splice(this.editedIndex, 1);
      this.model.subDepartments = JSON.stringify(this.subDepartments);
      this.closeDelete();
    },
    open() {
      this.dialog = true;
      this.subDepartmentsEditWindow = false;
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    subDepartmentsModalSave(editedItem) {
      this.editedItem = editedItem;
      this.save();
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.subDepartments[this.editedIndex], this.editedItem);
      } else {
        this.subDepartments.push(this.editedItem);
      }
      this.model.subDepartments = JSON.stringify(this.subDepartments);
      this.close();
    },
  },
};
</script>
