<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-row>
          <v-col sm="12">
            <va-select-input
              reference="jobTitles"
              v-model="model.jobTitleId"
              :label="$t('employees.jobTitleId')"
              :error-messages="jobTitleIdErrors"
            ></va-select-input>
          </v-col>
          <v-col sm="12">
            <va-select-input
              reference="educations"
              v-model="model.educationId"
              :filter="{ jobTitleId: item.jobTitleId }"
              :key="item.jobTitleId"
              :label="$t('employees.educationId')"
              @change="setEducationName($event)"
              :error-messages="educationIdErrors"
            ></va-select-input>
            <va-text-input
              v-show="false"
              v-model="model.educationName"
              :label="$t('employees.educationId')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-date-input
              v-model="model.educationStartDate"
              :label="$t('employees.educationStartDate')"
              @change="setValidityDate"
              format="short"
              :error-messages="educationStartDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-date-input
              v-model="model.educationEndDate"
              :label="$t('employees.educationEndDate')"
              format="short"
              :error-messages="educationEndDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-text-input
              v-model="model.educationValidityEnd"
              v-mask="'####-##-##'"
              :key="item.educationStartDate"
              :label="$t('employees.educationValidityEnd')"
              :error-messages="educationValidityEndErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-btn color="primary" dark class="mb-2" @click="save">
              {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { dateAddMonth } from "../../js/common";
import {
  required,
  // email,
  // minLength,
  // maxLength,
} from "vuelidate/lib/validators";

export default {
  props: ["edit"],
  data() {
    return {
      validity: null,
      model: {
        jobTitleId: null,
        educationId: null,
        educationName: null,
        educationStartDate: null,
        educationEndDate: null,
        educationValidityEnd: null,
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["employees/getDialogItem"];
      },
      set(model) {
        this.$store.commit("employees/setDialogItem", Object.assign({}, model));
      },
    },
    jobTitleIdErrors() {
      const errors = [];
      const field = "jobTitleId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationIdErrors() {
      const errors = [];
      const field = "educationId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationStartDateErrors() {
      const errors = [];
      const field = "educationStartDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationEndDateErrors() {
      const errors = [];
      const field = "educationEndDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    educationValidityEndErrors() {
      const errors = [];
      const field = "educationValidityEnd";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
  },
  validations: {
    model: {
      jobTitleId: {
        required,
      },
      educationId: {
        required,
      },
      educationStartDate: {
        required,
      },
      educationEndDate: {
        required,
      },
      educationValidityEnd: {
        required,
      },
    },
  },
  methods: {
    setEducationName(event) {
      this.model.educationName = event.name;
      this.validity = event.validity;
    },
    setValidityDate() {
      this.model.educationValidityEnd = dateAddMonth(
        this.model.educationStartDate,
        this.validity
      );
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
