<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-row>
          <v-col sm="12">
            <va-text-input
              v-show="false"
              :label="$t('employees.debitId')"
              v-model="model.debitId"
            ></va-text-input>
          </v-col>
          <v-col sm="12">
            <va-select-input
              reference="debititems"
              v-model="model.debitItemId"
              :label="$t('employees.debitItemId')"
              @change="setDebitItemName($event)"
              :error-messages="debitItemIdErrors"
            ></va-select-input>
            <va-text-input
              v-show="false"
              v-model="model.debitItemName"
              :label="$t('employees.debitItemId')"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-text-field v-if="employmentEndDate != null"
              v-model="dateFormatted"
              readonly
              :label="$t('employees.employmentEndDate')"
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-date-input
              v-model="model.debitStartDate"
              :label="$t('employees.debitStartDate')"
              format="short"
              :error-messages="debitStartDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-date-input
              v-model="model.debitEndDate"
              :label="$t('employees.debitEndDate')"
              format="short"
              :error-messages="debitEndDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-text-input
              v-model="model.debitItemSerialNo"
              :label="$t('employees.debitItemSerialNo')"
              :error-messages="debitItemSerialNoErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <va-text-input
              v-model="model.debitDescription"
              :label="$t('employees.debitDescription')"
              :error-messages="debitDescriptionErrors"
              multiline
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-btn color="primary" dark class="mb-2" @click="save">
              {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import {
  required,
  // email,
  // minLength,
  maxLength,
} from "vuelidate/lib/validators";

export default {
  props: ["edit", "employmentEndDate"],
  data() {
    return {
      validity: null,
      model: {
        debitId: null,
        debitItemId: null,
        debitItemName: null,
        debitStartDate: null,
        debitEndDate: null,
        debitItemSerialNo: null,
        debitDescription: null,
      },
    };
  },
  computed: {
    dateFormatted() {
      if (this.employmentEndDate != null) {
        return this.$d(new Date(String(this.employmentEndDate)), this.$admin.options.dateFormat)
      }
      return null
    },
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["employees/getDialogItem"];
      },
      set(model) {
        this.$store.commit("employees/setDialogItem", Object.assign({}, model));
      },
    },
    debitItemIdErrors() {
      const errors = [];
      const field = "debitItemId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    debitStartDateErrors() {
      const errors = [];
      const field = "debitStartDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    debitEndDateErrors() {
      const errors = [];
      const field = "debitEndDate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    debitItemSerialNoErrors() {
      const errors = [];
      const field = "debitItemSerialNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "60" }));
      return errors;
    },
    debitDescriptionErrors() {
      const errors = [];
      const field = "debitDescription";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  mounted() {
    this.model = this.item
  },
  validations: {
    model: {
      debitItemId: {
        required,
      },
      debitStartDate: {
        required,
      },
      debitEndDate: {
        required,
      },
      debitItemSerialNo: {
        maxLength: maxLength(60),
      },
      debitDescription: {
        maxLength: maxLength(255),
      },
    },
  },
  methods: {
    setDebitItemName(event) {
      this.model.debitItemName = event.name;
      // this.validity = event.validity;
    },
    // setValidityDate() {
    //   this.model.educationValidityEnd = dateAddMonth(
    //     this.model.educationStartDate,
    //     this.validity
    //   );
    // },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
