<template>
  <div>
    <v-row>
      <v-col sm="6" md="6">
        <va-text-input
          v-show="false"
          :label="$t('invoices.itemDepartmentName')"
          v-model="model.itemDepartmentName"
        ></va-text-input>
        <va-select-input
          v-model="model.itemDepartmentId"
          reference="departments"
          :filter="{ customerId: model.itemCustomerId }"
          :label="$t('invoices.itemDepartmentId')"
          @change="setDepartmentName($event)"
          :error-messages="itemDepartmentIdErrors"
        ></va-select-input>
      </v-col>
      <v-col sm="6" md="6">
        <va-text-input
          v-show="false"
          :label="$t('invoices.itemCurrencyName')"
          v-model="model.itemCurrencyName"
        ></va-text-input>
        <va-select-input
          source="itemCurrencyId"
          v-model="model.itemCurrencyId"
          :label="$t('invoices.itemCurrencyId')"
          @change="setCurrencyName($event)"
          :error-messages="itemCurrencyIdErrors"
        ></va-select-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="12" md="12">
        <va-text-input
          v-model="model.itemDescription"
          :label="$t('invoices.itemDescription')"
          multiline
        ></va-text-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-currency-input
          v-model="model.itemExchangeRate"
          :key="model.itemCurrencyId + '_itemExchangeRate'"
          :options="{ currency: 'TRY', precision: 4 }"
          :label="$t('invoices.itemExchangeRate')"
        ></va-currency-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="4">
        <va-currency-input
          v-model="model.itemAmount"
          :label="$t('invoices.itemAmount')"
          :key="model.itemCurrencyId + '_itemAmount'"
          :options="{ currency: currencyId, precision: 2 }"
        ></va-currency-input>
      </v-col>
      <v-col sm="4">
        <va-currency-input
          v-model="model.itemTaxAmount"
          :key="model.itemCurrencyId + '_itemTaxAmount'"
          :label="$t('invoices.itemTaxAmount')"
          :options="{ currency: currencyId, precision: 2 }"
        ></va-currency-input>
      </v-col>
      <v-col sm="4">
        <va-text-input
          v-model="model.itemBodyCount"
          :label="$t('invoices.itemBodyCount')"
          type="number"
        ></va-text-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-text-input
          v-show="false"
          :label="$t('invoices.invoiceItemId')"
          v-model="model.invoiceItemId"
        ></va-text-input>
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="6" md="6">
        <va-file-input
          source="model.itemFile"
          v-model="model.itemFile"
          base64
          tableName="invoiceItems"
          lg="4"
        >
        </va-file-input>
      </v-col>
      <v-col sm="6" md="6">
      </v-col>
    </v-row>
    <v-row>
      <v-col sm="12">
        <v-btn color="primary" dark class="mb-2" @click="save">
          {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { required } from "vuelidate/lib/validators";

export default {
  props: ["edit", "customerId"],
  data() {
    return {
      currencyId: null,
      model: {
        invoiceItemId: "",
        itemCustomerId: this.customerId,
        itemDepartment: null,
        itemDepartmentId: "",
        itemDepartmentName: "",
        itemCurrency: null,
        itemCurrencyName: "",
        itemCurrencyId: "",
        itemExchangeRate: "0",
        itemAmount: "0",
        itemTaxAmount: "0",
        itemDescription: null,
        itemFile: null,
        itemBodyCount: "0",
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["invoices/getDialogItem"];
      },
      set(model) {
        this.$store.commit(`invoices/setDialogItem`, Object.assign({}, model));
      },
    },
    itemCurrencyIdErrors() {
      const errors = [];
      const field = "itemCurrencyId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    itemDepartmentIdErrors() {
      const errors = [];
      const field = "itemDepartmentId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
    // console.error(this.item)
    this.currencyId = this.item.itemCurrencyId
      ? this.item.itemCurrencyId
      : "TRY";
  },
  validations: {
    model: {
      itemCurrencyId: {
        required,
      },
      itemDepartmentId: {
        required,
      },
    },
  },
  methods: {
    detectCurrencyInput() {
      if (this.model.itemCurrencyId) {
        this.currencyId = this.model.itemCurrencyId;
      }
    },
    setDepartmentName(event) {
      this.model.itemDepartmentName = event.name;
      this.model.itemDepartmentId = event.id;
    },
    setCurrencyName(event) {
      this.model.itemCurrencyName = event.name;
      this.model.itemCurrencyId = event.id;
      this.detectCurrencyInput();
    },
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
