<template>
  <div>
    <v-row>
      <v-col sm="12">
        <v-row>
          <v-col sm="12" md="8">
            <va-text-input
              v-show="false"
              v-model="model.childId"
              :label="$t('employees.childId')"
            ></va-text-input>
            <va-text-input
              v-model="model.childNameSurname"
              :label="$t('employees.childNameSurname')"
              :error-messages="childNameSurnameErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12" md="8">
            <va-text-input
              v-model="model.childTckn"
              :label="$t('employees.childTckn')"
              :error-messages="childTcknErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12" md="8">
            <va-date-input
              v-model="model.childBirthdate"
              :label="$t('employees.childBirthdate')"
              format="short"
              :error-messages="childBirthDateErrors"
            ></va-date-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12" md="8">
            <va-text-input
              v-model="model.childSchoolName"
              :label="$t('employees.childSchoolName')"
              :error-messages="childSchoolNameErrors"
            ></va-text-input>
          </v-col>
        </v-row>
        <v-row>
          <v-col sm="12">
            <v-btn color="primary" dark class="mb-2" @click="save">
              {{ edit ? $t("dialog.saveButton") : $t("dialog.addButton") }}
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import { required, minLength, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["edit"],
  data() {
    return {
      model: {
        childId: null,
        childNameSurname: null,
        childTckn: null,
        childBirthdate: null,
        childSchoolName: null,
      },
    };
  },
  computed: {
    item: {
      get() {
        if (this.edit == false) {
          return this.model;
        }
        return this.$store.getters["employees/getDialogItem"];
      },
      set(model) {
        this.$store.commit("employees/setDialogItem", Object.assign({}, model));
      },
    },
    childNameSurnameErrors() {
      const errors = [];
      const field = "childNameSurname";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    childTcknErrors() {
      const errors = [];
      const field = "childTckn";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].minLength &&
        errors.push(this.$t("v.string.minLength", { min: "11" }));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "11" }));
      return errors;
    },
    childBirthDateErrors() {
      const errors = [];
      const field = "childBirthdate";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      return errors;
    },
    childSchoolNameErrors() {
      const errors = [];
      const field = "childSchoolName";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "160" }));
      return errors;
    },
  },
  mounted() {
    this.model = this.item;
  },
  validations: {
    model: {
      childNameSurname: {
        required,
      },
      childTckn: {
        minLength: minLength(11),
        maxLength: maxLength(11),
      },
      childBirthdate: {
        required,
      },
      childSchoolName: {
        maxLength: maxLength(160),
      },
    },
  },
  methods: {
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.$emit("save", this.model);
    },
  },
};
</script>
