<template>
  <div>
    <v-card>
      <v-card-text>
        <va-list
          ref="obj"
          :filters="filters"
          :items-per-page="10"
          :disableExport="true"
          :disableCreate="true"
          :disable-items-per-page="false"
          :disable-global-search="true"
          @selected="onSelect"
          @update:filter="onUpdateFilter"
        >
        <va-data-table
            :fields="fields"
            disable-create-redirect
            class="v-data-table__editable_wrapper"
          >
            <template v-slot:[`field.amount`]="{ item }">
              {{ item.amount | formatMoney(item.currencyId, 2) }}
            </template>
            <template v-slot:[`field.tax`]="{ item }">
              {{ item.tax | formatMoney("TRY", 2) }}
            </template>
            <template v-slot:[`field.totalAmount`]="{ item }">
              {{ item.totalAmount | formatMoney(item.currencyId, 2) }}
            </template>
            <template v-slot:[`field.customerId`]="{ item }">
              <v-chip :color="item.customerColor" text-color="white">
                {{ item.customerShortName }}
              </v-chip>
            </template>
            <template v-slot:[`field.whoPaidId`]="{ item }">
              {{ (item.whoPaidId == 'pernet') ? "Pernet" : "alan" }}
            </template>
        </va-data-table>
        <template v-slot:actions>
            <v-menu offset-y>
              <template v-slot:activator="{ on }">
                <v-btn
                  text
                  color="success"
                  v-on="on"
                  :icon="!$vuetify.breakpoint.lgAndUp"
                >
                  <v-icon small>mdi-export</v-icon>
                  <span v-if="$vuetify.breakpoint.lgAndUp" class="ml-2">{{
                    $t("dialog.excelExportTitle")
                  }}</span>
                </v-btn>
              </template>
              <v-list>
                <v-list-item
                  v-for="(option) in getExportOptions()"
                  :key="option.key"
                  @click="excelExport(option)"
                >
                  <v-list-item-title>{{ option.label }}</v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
<!--           <va-excel-export-button @click="excelExportOpen"></va-excel-export-button> -->
          <va-batch-update-button @click="open"></va-batch-update-button>
        </template>
        </va-list>
      </v-card-text>

      <v-dialog v-model="dialog" max-width="400px">
        <v-card>
          <v-card-title>
            <span class="text-h5">{{ $t("dialog.batchUpdateTitle") }}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <expensereports-batch-update
                :key="renderKey"
              >
              </expensereports-batch-update>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">
              {{ $t("dialog.closeButton") }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-card>
  </div>
</template>

<script>
import { required, minLength, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["resource"], // "title"
  provide() {
    return {
        validations: {
          customerId: { required },
          workplaceId: { required },
          sowId: { required },
          employeeId: { required },
          currencyId: { required },
          expenseTypeId: { required },
          whoPaidId: { required },
          docNumber: {
            minLength: minLength(3),
            maxLength: maxLength(100),
          },
        },
        errorMessages: {
          customerIdErrors() {
            return [{ required: null }];
          },
          workplaceIdErrors() {
            return [{ required: null }];
          },
          sowIdErrors() {
            return [{ required: null }];
          },
          currencyIdErrors() {
            return [{ required: null }];
          },
          employeeIdErrors() {
            return [{ required: null }];
          },
          expenseTypeIdErrors() {
            return [{ required: null }];
          },
          whoPaidIdErrors() {
            return [{ required: null }];
          },
          docNumberErrors() {
            return [{ minLength: 3 }, { maxLength: 100 }];
          },
        },
      };
  },
  data() {
    return {
      dialog: false,
      dialogExcel: false,
      selected: [],
      renderKey: 0,
      filters: [
        {
          source: "customerId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "customers",
          },
          filterId: "customerId",
          label: this.$t("expensereports.customerId"),
          alwaysOn: true,
        },
        {
          source: "emptyPo",
          type: "boolean",
          label: this.$t("expensereports.emptyPo"),
          alwaysOn: true,
        },
        {
          source: "notEmptyPo",
          type: "boolean",
          label: this.$t("expensereports.notEmptyPo"),
          alwaysOn: true,
        },
        {
          source: "emptyPackageNo",
          type: "boolean",
          label: this.$t("expensereports.emptyPackageNo"),
          alwaysOn: true,
        },
        {
          source: "notEmptyPackageNo",
          type: "boolean",
          label: this.$t("expensereports.notEmptyPackageNo"),
          alwaysOn: true,
        },
        {
          source: "poNo",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "poNumbers",
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensereports.poNo"),
        },
        {
          source: "expenseTypeId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "expenseTypes",
          },
          label: this.$t("expensereports.expenseTypeId"),
          alwaysOn: true,
        },
        {
          source: "paymentTypeId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "paymentTypes",
          },
          label: this.$t("expensereports.paymentTypeId"),
        },
        {
          source: "sowId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "sows"
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensereports.sowId"),
          alwaysOn: false,
        },
        {
          source: "workplaceId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "workplaces",
          },
          label: this.$t("expensereports.workplaceId"),
        },
        {
          source: "employeeId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "employees",
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensereports.employeeComplete"),
        },
        {
          source: "docNumber",
          type: "text",
          label: this.$t("expensereports.docNumber"),
        },
        {
          source: "confirmNo",
          type: "text",
          label: this.$t("expensereports.confirmNo"),
        },
        // {
        //   source: "confirmStatus",
        //   type: "boolean",
        //   label: this.$t("expensereports.confirmStatus"),
        // },
        {
          source: "pernetInvoiceNo",
          type: "text",
          label: this.$t("expensereports.pernetInvoiceNo"),
        },
        {
          source: "expenseApplication",
          type: "text",
          label: this.$t("expensereports.expenseApplication"),
        },
        {
          source: "packageNo",
          type: "text",
          label: this.$t("expensereports.packageNo"),
        },
        {
          source: "expenseDateStart",
          type: "date",
          label: this.$t("expensereports.expenseDateStart"),
        },
        {
          source: "expenseDateEnd",
          type: "date",
          label: this.$t("expensereports.expenseDateEnd"),
        },
        {
          source: "sentForApproval",
          type: "boolean",
          label: this.$t("expensereports.sentForApproval"),
        },
      ],
      fields: [
        {
          source: "customerId",
          sourceLabel: "customerShortName",
          // attributes: {
          //   multiple: true
          // },
          type: "select",
          reference: "customers",
          filterId: "customerId",
          label: this.$t("expensereports.customerName"),
          sortable: true,
        },
        {
          source: "workplaceId",
          sourceLabel: "workplaceName",
          type: "select",
          reference: "workplaces",
          label: this.$t("expensereports.workplaceName"),
          sortable: true,
        },
        {
          source: "sowId",
          sourceLabel: "sowName",
          type: "select",
          reference: "sows",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensereports.sowName"),
          sortable: true,
        },
        {
          source: "departmentId",
          sourceLabel: "departmentName",
          type: "select",
          reference: "departments",
          filterId: "departmentId",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensereports.departmentName"),
          sortable: true,
        },
        {
          source: "subDepartmentId",
          sourceLabel: "subDepartmentName",
          type: "select",
          reference: "subDepartments",
          label: this.$t("expensereports.subDepartmentName"),
          key: "departmentId",
          filters: ["departmentId"],
          sortable: true,
        },
        {
          source: "employeeId",
          sourceLabel: "employeeName",
          type: "select",
          reference: "employees",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expensereports.employeeName"),
          sortable: true,
        },
        {
          source: "expenseTypeId",
          sourceLabel: "expenseTypeName",
          type: "select",
          reference: "expenseTypes",
          label: this.$t("expensereports.expenseTypeId"),
          sortable: true,
        },
        {
          source: "paymentTypeId",
          sourceLabel: "paymentTypeName",
          type: "select",
          reference: "paymentTypes",
          label: this.$t("expensereports.paymentTypeId"),
          sortable: true,
        },
        {
          source: "description",
          label: this.$t("expensereports.description"),
          sortable: true,
        },
        {
          source: "expenseDate",
          type: "date",
          label: this.$t("expensereports.expenseDate"),
          sortable: true,
        },
        {
          source: "docNumber",
          label: this.$t("expensereports.docNumber"),
          sortable: true,
        },
        {
          source: "currencyId",
          type: "select",
          label: this.$t("expensereports.currencyId"),
          sortable: true,
        },
        {
          source: "amount",
          type: "currency",
          options: { id: "currency", source: "currencyId", default:"TRY", extra: { precision: 2 } },
          label: this.$t("expensereports.amount"),
          sortable: true,
        },
        {
          source: "tax",
          type: "currency",
          options: { id: "currency", source: "currencyId", default:"TRY", extra: { precision: 2 } },
          label: this.$t("expensereports.tax"),
          sortable: true,
        },
        {
          source: "totalAmount",
          type: "currency",
          options: { id: "currency", source: "currencyId", default:"TRY", extra: { precision: 2 } },
          label: this.$t("expensereports.totalAmount"),
          sortable: true,
        },
        {
          source: "whoPaidId",
          sourceLabel: "whoPaidId",
          type: "select",
          label: this.$t("expensereports.whoPaidId"),
          sortable: true,
        },
        {
          source: "isEmployeePaid",
          label: this.$t("expensereports.isEmployeePaid"),
          type: "boolean",
          sortable: true,
        },
        {
          source: "isSellerPaid",
          label: this.$t("expensereports.isSellerPaid"),
          type: "boolean",
          sortable: true,
        },
        {
          source: "confirmNo",
          label: this.$t("expensereports.confirmNo"),
          sortable: true,
        },
        {
          source: "poNo",
          label: this.$t("expensereports.poNo"),
          sortable: true,
        },
        {
          source: "pernetInvoiceNo",
          label: this.$t("expensereports.pernetInvoiceNo"),
          sortable: true,
        },
        // {
        //   source: "confirmStatus",
        //   label: this.$t("expensereports.confirmStatus"),
        //   type: "boolean",
        //   sortable: true,
        // },
        {
          source: "expenseApplication",
          label: this.$t("expensereports.expenseApplication"),
          sortable: true,
        },
        {
          source: "packageNo",
          label: this.$t("expensereports.packageNo"),
          sortable: true,
        },
        {
          source: "sentForApproval",
          label: this.$t("expensereports.sentForApproval"),
          type: "boolean",
          sortable: true,
        },
      ],
    };
  },
  watch: {
    dialog(val) {
      if (val == true) {
        this.renderModalComponent();
      }
      val || this.close();
    },
    dialogExcel(val) {
      if (val == true) {
        this.renderModalComponent();
      }
      val || this.close();
    },
  },
  methods: {
    renderModalComponent() {
      this.renderKey = this.renderKey + 1;
    },
    open() {
      this.dialog = true;
    },
    close() {
      this.dialog = false;
    },
    onSelect(selected) {
      this.selected = selected;
    },
    onUpdateFilter(val) {
      let filter = {
        source: "poNo",
        type: "selectfilter",
        attributes: {
          optionText: "name",
          multiple: true,
          reference: "poNumbers",
        },
        label: this.$t("expensereports.poNo"),
      }
      if (val.notEmptyPo == 1) {
        this.$refs.obj.enableFilter(filter)
      } else if (val.notEmptyPo == 0) {
        this.$refs.obj.disableFilter(filter)
      }
    },
    getExportOptions() {
      return [
         { key: "all", label: "Tm Stunlar" },
         { key: "expense", label: "Masraf Tablosu" },
         { key: "ds", label: "Masraf Tablosu D&S" },
      ]
    },
    openExcel() {
      this.dialogExcel = true;
    },
    closeExcel() {
      this.dialogExcel = false;
    },
    async excelExport(option) {
      let exportUrl = this.$admin.apiUrl + '/expensereports/export?key=' + option.key
      location.href = exportUrl
    }
  },
};
</script>
