<template>
  <div>
    <v-card>
      <v-card-text>
        <va-list
          :filters="filters"
          :items-per-page="10"
          :disableExport="true"
          :disableCreate="true"
          :disable-items-per-page="false"
          :disable-global-search="true"
          @update:filter="onFilterChange"
          @selected="onSelect"
        >
          <template v-if="customerId">
            <va-data-table
              :fields="fields"
              row-create
              row-edit
              disable-create-redirect
              class="v-data-table__editable_wrapper"
            >
              <template v-slot:[`field.customerId`]="{ item }">
                <v-chip :color="item.customerColor" text-color="white">
                  {{ item.customerShortName }}
                </v-chip>
              </template>
              <template v-slot:[`field.amount`]="{ item }">
                {{ item.amount | formatMoney(item.currencyId, 2) }}
              </template>
              <template v-slot:[`field.tax`]="{ item }">
                {{ item.tax | formatMoney("TRY", 2) }}
              </template>
              <template v-slot:[`field.totalAmount`]="{ item }">
                {{ item.totalAmount | formatMoney(item.currencyId, 2) }}
              </template>
              <template v-slot:[`field.whoPaidId`]="{ item }">
                {{ (item.whoPaidId == 'pernet') ? "Pernet" : "alan" }}
              </template>
            </va-data-table>
          </template>
          <template v-else>
            <v-alert dense outlined type="error">
                {{ $t("alerts.firstChooseCustomer") }}
            </v-alert>
          </template>
          <template v-slot:actions>
            <va-action-button
              :label="$t('expenses.importExcel')"
              icon="mdi-import"
              color="success"
              text
              @click="importExcel"
            ></va-action-button>
          </template>          
        </va-list>
      </v-card-text>

      <v-dialog v-model="dialog" :max-width="width">
        <v-card>
          <v-card-title>
            <span class="text-h5">{{ $t("dialog.importExcelTitle") }}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <expenses-import-excel @width="onWidth"
                :key="renderKey"
              >
              </expenses-import-excel>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="close">
              {{ $t("dialog.closeButton") }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </v-card>
  </div>
</template>

<script>
import isEmpty from "lodash/isEmpty";
import { required, minLength, maxLength } from "vuelidate/lib/validators";

export default {
  props: ["resource", "title"], 
  provide() {
    return {
        validations: {
          customerId: { required },
          workplaceId: { required },
          sowId: { required },
          employeeId: { required },
          currencyId: { required },
          expenseTypeId: { required },
          whoPaidId: { required },
          docNumber: {
            minLength: minLength(3),
            maxLength: maxLength(100),
          },
        },
        errorMessages: {
          customerIdErrors() {
            return [{ required: null }];
          },
          workplaceIdErrors() {
            return [{ required: null }];
          },
          sowIdErrors() {
            return [{ required: null }];
          },
          currencyIdErrors() {
            return [{ required: null }];
          },
          employeeIdErrors() {
            return [{ required: null }];
          },
          expenseTypeIdErrors() {
            return [{ required: null }];
          },
          whoPaidIdErrors() {
            return [{ required: null }];
          },
          docNumberErrors() {
            return [{ minLength: 3 }, { maxLength: 100 }];
          },
        },
      };
  },
  data() {
    return {
      dialog: false,
      width: "600px",
      renderKey: 0,
      customerId: null,
      filterForm: null,
      filters: [
        {
          source: "customerId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "customers"
          },
          filterId: "customerId",
          label: this.$t("expenses.customerId"),
          alwaysOn: true
        },
        {
          source: "sowId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "sows"
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.sowId"),
          alwaysOn: false
        },
        {
          source: "workplaceId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "workplaces",
          },
          label: this.$t("expenses.workplaceId"),
        },
        {
          source: "employeeId",
          type: "autocompletefilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "employees",
            // vif: "customerId",
          },
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.employeeComplete"),
          alwaysOn: true
        },
        {
          source: "docNumber",
          type: "text",
          label: this.$t("expenses.docNumber"),
          alwaysOn: false
        },
        // {
        //   source: "confirmNo",
        //   type: "text",
        //   label: this.$t("expenses.confirmNo"),
        // },
        // {
        //   source: "confirmStatus",
        //   type: "boolean",
        //   label: this.$t("expenses.confirmStatus"),
        // },
        {
          source: "sentForApproval",
          type: "boolean",
          label: this.$t("expenses.sentForApproval"),
        },
        {
          source: "expenseTypeId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "expenseTypes",
          },
          label: this.$t("expenses.expenseTypeId"),
        },
        {
          source: "paymentTypeId",
          type: "selectfilter",
          attributes: {
            optionText: "name",
            multiple: true,
            reference: "paymentTypes",
          },
          label: this.$t("expenses.paymentTypeId"),
        },
        {
          source: "expenseApplication",
          type: "text",
          label: this.$t("expenses.expenseApplication"),
        },
        {
          source: "packageNo",
          type: "text",
          label: this.$t("expenses.packageNo"),
        },
        {
          source: "expenseDateStart",
          type: "date",
          label: this.$t("expenses.expenseDateStart"),
          alwaysOn: true
        },
        {
          source: "expenseDateEnd",
          type: "date",
          label: this.$t("expenses.expenseDateEnd"),
          alwaysOn: true
        },
      ],
      fields: [
        {
          source: "customerId",
          sourceLabel: "customerShortName",
          // attributes: {
          //   multiple: true
          // },
          type: "select",
          reference: "customers",
          filterId: "customerId",
          label: this.$t("expenses.customerName"),
          sortable: true,
          editable: false,
        },
        {
          source: "workplaceId",
          sourceLabel: "workplaceName",
          type: "select",
          reference: "workplaces",
          label: this.$t("expenses.workplaceName"),
          sortable: true,
          editable: false,
        },
        {
          source: "sowId",
          sourceLabel: "sowName",
          type: "select",
          reference: "sows",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.sowName"),
          sortable: true,
          editable: false,
        },
        {
          source: "departmentId",
          sourceLabel: "departmentName",
          type: "select",
          reference: "departments",
          filterId: "departmentId",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.departmentName"),
          sortable: true,
          editable: false,
        },
        {
          source: "subDepartmentId",
          sourceLabel: "subDepartmentName",
          type: "select",
          reference: "subDepartments",
          label: this.$t("expenses.subDepartmentName"),
          key: "departmentId",
          filters: ["departmentId"],
          sortable: true,
          editable: false,
        },
        {
          source: "employeeId",
          sourceLabel: "employeeName",
          type: "select",
          reference: "employees",
          key: "customerId",
          filters: ["customerId"],
          label: this.$t("expenses.employeeName"),
          sortable: true,
          editable: false,
        },
        {
          source: "expenseTypeId",
          sourceLabel: "expenseTypeName",
          type: "select",
          reference: "expenseTypes",
          label: this.$t("expenses.expenseTypeId"),
          sortable: true,
          editable: true,
        },
        {
          source: "paymentTypeId",
          sourceLabel: "paymentTypeName",
          type: "select",
          reference: "paymentTypes",
          label: this.$t("expenses.paymentTypeId"),
          sortable: true,
          editable: true,
        },
        {
          source: "description",
          label: this.$t("expenses.description"),
          sortable: true,
          editable: true,
        },
        {
          source: "expenseDate",
          type: "date",
          label: this.$t("expenses.expenseDate"),
          sortable: true,
          editable: true,
        },
        {
          source: "docNumber",
          label: this.$t("expenses.docNumber"),
          sortable: true,
        },
        {
          source: "currencyId",
          type: "select",
          label: this.$t("expenses.currencyId"),
          sortable: true,
          editable: true,
        },
        {
          source: "amount",
          type: "currency",
          options: { id: "currency", source: "currencyId", default:"TRY", extra: { precision: 2 } },
          label: this.$t("expenses.amount"),
          sortable: true,
          editable: true,
        },
        {
          source: "tax",
          type: "currency",
          options: { id: "currency", source: "currencyId", default:"TRY", extra: { precision: 2 } },
          label: this.$t("expenses.tax"),
          sortable: true,
          // editable: true,
        },
        {
          source: "totalAmount",
          type: "currency",
          options: { id: "currency", source: "currencyId", default:"TRY", extra: { precision: 2 } },
          label: this.$t("expenses.totalAmount"),
          sortable: true,
          // editable: true,
        },
        {
          source: "whoPaidId",
          sourceLabel: "whoPaidId",
          type: "select",
          label: this.$t("expenses.whoPaidId"),
          sortable: true,
          editable: true,
        },
        {
          source: "isEmployeePaid",
          label: this.$t("expenses.isEmployeePaid"),
          type: "boolean",
          sortable: true,
          editable: true,
        },
        {
          source: "isSellerPaid",
          label: this.$t("expenses.isSellerPaid"),
          type: "boolean",
          sortable: true,
          editable: true,
        },
        // {
        //   source: "confirmNo",
        //   label: this.$t("expenses.confirmNo"),
        //   sortable: true,
        // },
        // {
        //   source: "confirmStatus",
        //   label: this.$t("expenses.confirmStatus"),
        //   type: "boolean",
        //   sortable: true,
        // },
        {
          source: "sentForApproval",
          label: this.$t("expenses.sentForApproval"),
          type: "boolean",
          sortable: true,
          editable: true,
        },
        {
          source: "expenseApplication",
          label: this.$t("expenses.expenseApplication"),
          sortable: true,
        },
        {
          source: "packageNo",
          label: this.$t("expenses.packageNo"),
          sortable: true,
        },
        {
          source: "file",
          label: this.$t("expenses.file"),
          type: "file",
          attributes: {
            base64: true,
            tableName: "expenseFiles"
          },
          sortable: false,
        },
      ],

    };
  },
  watch: {
    dialog(val) {
      if (val == true) {
        this.renderModalComponent()
      }
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },
  methods: {
    onWidth(w) {
      this.width = w
    },
    onFilterChange(newVal) {
      this.$store.commit("expenses/setFilterForm", newVal);
      let customerSelected = Object.prototype.hasOwnProperty.call(newVal, 'customerId')
      if (customerSelected && ! isEmpty(newVal.customerId)) {
        this.customerId = true
      } else {
        this.customerId = false
      }
    },
    renderModalComponent() {
      this.renderKey = this.renderKey + 1
    },
    open() {
      this.dialog = true
    },
    close() {
      this.dialog = false
      this.width = "600px";
    },
    onSelect(selected) {
      this.selected = selected
    },
    async importExcel() {
      this.open()
      return 
    }
  },
};
</script>
