<template>
  <div>
    <va-form
      :id="id"
      :item="item"
      @validate="onValidate"
      v-model="model"
      disable-redirect
    >
      <v-card>
        <v-tabs v-model="tabs">
          <v-tab>{{ $t("orders.tab1") }}</v-tab>
          <v-tab
            >{{ $t("orders.tab3") }} ({{ invoiceOrderItems.length }})</v-tab
          >
        </v-tabs>

        <v-tabs-items v-model="tabs">
          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="3">
                    <va-select-input
                      source="customerId"
                      reference="customers"
                      @change="setCustomerId($event)"
                      :error-messages="customerIdErrors"
                    ></va-select-input>
                    <va-text-input
                      v-show="false"
                      source="id"
                      :label="$t('orders.id')"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="purchaseOrderNo"
                      :error-messages="purchaseOrderNoErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3">
                    <va-text-input
                      source="customerOrderNo"
                      :error-messages="customerOrderNoErrors"
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3"> </v-col>
                </v-row>
                <v-row>
                  <v-col sm="9">
                    <va-select-input
                      source="sows"
                      reference="sows"
                      :filter="{ customerId: model.customerId }"
                      :key="model.customerId"
                      :error-messages="sowsErrors"
                      multiple
                    >
                    </va-select-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="2">
                    <va-select-input
                      source="startYearId"
                      @change="detectTotalMonths"
                      reference="years"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-select-input
                      source="endYearId"
                      @change="detectTotalMonths"
                      reference="years"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-select-input
                      source="startMonthId"
                      @change="detectTotalMonths"
                      reference="months"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-select-input
                      source="endMonthId"
                      @change="detectTotalMonths"
                      reference="months"
                    ></va-select-input>
                  </v-col>
                  <v-col sm="2">
                    <va-text-input
                      source="totalMonth"
                      v-model="model.totalMonth"
                    ></va-text-input>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col sm="6">
                    <va-text-input
                      source="description"
                      :error-messages="descriptionErrors"
                      multiline
                    ></va-text-input>
                  </v-col>
                  <v-col sm="3"> </v-col>
                </v-row>
                <v-row>
                  <v-col sm="2">
                    <va-date-input
                      source="startDate"
                      format="short"
                    ></va-date-input>
                  </v-col>
                  <v-col sm="2">
                    <va-date-input
                      source="endDate"
                      format="short"
                    ></va-date-input>
                  </v-col>
                  <v-col sm="2">
                    <va-boolean-input source="grConsent"></va-boolean-input>
                  </v-col>
                  <v-col sm="2">
                    <va-boolean-input source="flexyForce"></va-boolean-input>
                  </v-col>
                </v-row>

                <v-row>
                  <v-col sm="12">
                    <va-text-input
                      v-show="false"
                      :label="$t('orders.purchaseOrderItems')"
                      source="model.purchaseOrderItems"
                      v-model="model.purchaseOrderItems"
                    ></va-text-input>
                    <template>
                      <v-data-table
                        :headers="headers"
                        :items="purchaseOrderItems"
                        sort-by="name"
                        class="elevation-1"
                        hide-default-footer
                      >
                        <template v-slot:item.itemExchangeRate="{ item }">
                          {{ item.itemExchangeRate | formatMoney("TRY", 4) }}
                        </template>
                        <template v-slot:item.itemAmount="{ item }">
                          {{
                            item.itemAmount
                              | formatMoney(item.itemCurrencyId, 2)
                          }}
                        </template>
                        <template v-slot:item.itemTaxAmount="{ item }">
                          {{
                            item.itemTaxAmount
                              | formatMoney(item.itemCurrencyId, 2)
                          }}
                        </template>
                        <template v-slot:item.itemTotalTaxAmount="{ item }">
                          {{
                            item.itemTotalTaxAmount
                              | formatMoney(item.itemCurrencyId, 2)
                          }}
                        </template>
                        <template v-slot:item.itemTotalAmount="{ item }">
                          {{
                            item.itemTotalAmount
                              | formatMoney(item.itemCurrencyId, 2)
                          }}
                        </template>
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-toolbar-title>{{
                              $t("orders.purchaseOrderItems")
                            }}</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                            <v-dialog v-model="dialog" max-width="800px">
                              <template v-slot:activator="{ attrs }">
                                <v-btn
                                  color="primary"
                                  dark
                                  class="mb-2"
                                  v-bind="attrs"
                                  @click="open"
                                >
                                  {{ $t("orders.purchaseOrderButton") }}
                                </v-btn>
                              </template>
                              <v-card>
                                <v-card-title>
                                  <span class="text-h5">{{ formTitle }}</span>
                                </v-card-title>
                                <v-card-text>
                                  <v-container>
                                    <orders-orderitems
                                      :edit="orderItemsEditWindow"
                                      :key="renderKeyOrderItems"
                                      :customerId="model.customerId"
                                      @save="orderItemsModalSave"
                                    >
                                    </orders-orderitems>
                                  </v-container>
                                </v-card-text>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="close"
                                  >
                                    {{ $t("dialog.cancelButton") }}
                                  </v-btn>
                                  <!--                                   <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="save"
                                  >
                                    {{ $t("dialog.saveButton") }}
                                  </v-btn> -->
                                </v-card-actions>
                              </v-card>
                            </v-dialog>

                            <v-dialog v-model="dialogDelete" max-width="500px">
                              <v-card>
                                <v-card-title class="text-h5">{{
                                  $t("dialog.deleteConfirm")
                                }}</v-card-title>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="closeDelete"
                                    >{{ $t("dialog.closeDeleteButton") }}</v-btn
                                  >
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="deleteItemConfirm"
                                    >{{
                                      $t("dialog.confirmDeleteButton")
                                    }}</v-btn
                                  >
                                  <v-spacer></v-spacer>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.actions="{ item }">
                          <v-icon small class="mr-2" @click="editItem(item)">
                            mdi-pencil
                          </v-icon>
                          <v-icon small @click="deleteItem(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                        <!-- <template v-slot:no-data></template> -->

                        <!--                   <template v-slot:[`field.itemDepartmentId`]="{ editedItem }">
                    <span>{{ new Date(item.calculationDate).toLocaleDateString("tr") }}</span>
                  </template> -->
                      </v-data-table>
                    </template>
                  </v-col>
                </v-row>

                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>

          <v-tab-item>
            <v-card flat>
              <v-card-text>
                <v-row>
                  <v-col sm="12">
                    <va-text-input
                      v-show="false"
                      :label="$t('orders.invoiceOrderItems')"
                      source="model.invoiceOrderItems"
                      v-model="model.invoiceOrderItems"
                    ></va-text-input>
                    <template>
                      <v-data-table
                        :headers="headersInvoiceOrderItems"
                        :items="invoiceOrderItems"
                        sort-by="name"
                        class="elevation-1"
                        hide-default-footer
                      >
                        <template v-slot:top>
                          <v-toolbar flat>
                            <v-spacer></v-spacer>
                            <v-dialog
                              v-model="dialogIIDelete"
                              max-width="500px"
                            >
                              <v-card>
                                <v-card-title class="text-h5">{{
                                  $t("dialog.deleteConfirm")
                                }}</v-card-title>
                                <v-card-actions>
                                  <v-spacer></v-spacer>
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="closeIIDelete"
                                    >{{ $t("dialog.closeDeleteButton") }}</v-btn
                                  >
                                  <v-btn
                                    color="blue darken-1"
                                    text
                                    @click="deleteIIitemConfirm"
                                    >{{
                                      $t("dialog.confirmDeleteButton")
                                    }}</v-btn
                                  >
                                  <v-spacer></v-spacer>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                          </v-toolbar>
                        </template>
                        <template v-slot:item.actions="{ item }">
                          <v-icon small @click="deleteIIitem(item)">
                            mdi-delete
                          </v-icon>
                        </template>
                        <!-- <template v-slot:no-data></template> -->

                        <!--                   <template v-slot:[`field.itemDepartmentId`]="{ editedItem }">
                    <span>{{ new Date(item.calculationDate).toLocaleDateString("tr") }}</span>
                  </template> -->
                      </v-data-table>
                    </template>
                  </v-col>
                </v-row>
                <va-save-button></va-save-button>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </va-form>
  </div>
</template>

<script>
/// https://stackoverflow.com/questions/54344033/vuelidate-validate-form-with-sub-components
///
import { generateId, findTotalMonths } from "../../js/common";
import { required, maxLength } from "vuelidate/lib/validators";
import axios from "axios";

export default {
  props: ["id", "item"],
  provide() {
    return {
      $v: this.$v,
    };
  },
  data() {
    return {
      tabs: null,
      renderKeyOrderItems: 1001,
      orderItemsEditWindow: false,
      model: {
        id: "",
        customerId: "",
        sows: null,
        purchaseOrderNo: null,
        customerOrderNo: null,
        description: null,
        startDate: null,
        endDate: null,
        startYearId: null,
        endYearId: null,
        startMonthId: null,
        endMonthId: null,
        totalMonth: null,
        grConsent: false,
        flexyForce: false,
        invoiceOrderItems: "",
        purchaseOrderItems: "",
      },
      currencies: [
        { id: "TRY", name: "Trk Liras" },
        { id: "USD", name: "Dolar" },
        { id: "EUR", name: "Euro" },
      ],
      departments: [],
      invoiceOrderItems: [],
      purchaseOrderItems: [],
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 2,
        masked: false,
      },
      showExchangeRate: false,
      exchangeRate: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " TL",
        precision: 4,
        masked: false,
      },
      dialog: false,
      dialogDelete: false,
      dialogIIDelete: false,
      headers: [
        { text: this.$t("orders.itemDescription"), value: "itemDescription" },
        {
          text: this.$t("orders.itemDepartmentName"),
          value: "itemDepartmentName",
        },
        { text: this.$t("orders.itemCurrencyName"), value: "itemCurrencyName" },
        { text: this.$t("orders.itemExchangeRate"), value: "itemExchangeRate" },
        { text: this.$t("orders.itemAmount"), value: "itemAmount" },
        { text: this.$t("orders.itemTaxAmount"), value: "itemTaxAmount" },
        { text: this.$t("orders.itemTotalAmount"), value: "itemTotalAmount" },
        {
          text: this.$t("orders.itemTotalTaxAmount"),
          value: "itemTotalTaxAmount",
        },
        {
          text: this.$t("orders.purchaseOrderItemsActions"),
          value: "actions",
          sortable: false,
        },
      ],
      headersInvoiceOrderItems: [
        { text: this.$t("orders.invoiceNo"), value: "invoiceNo" },
        { text: this.$t("orders.eInvoiceNo"), value: "eInvoiceNo" },
        { text: this.$t("orders.departmentName"), value: "itemDepartmentName" },
        { text: this.$t("orders.startDate"), value: "startDate" },
        { text: this.$t("orders.endDate"), value: "endDate" },
        { text: this.$t("orders.description"), value: "itemDescription" },
        { text: this.$t("orders.currencyId"), value: "itemCurrencyId" },
        { text: this.$t("orders.totalAmount"), value: "itemTotalAmount" },
        { text: this.$t("orders.orderActions"), value: "actions" },
      ],
      editedIndex: -1,
      IIeditedIndex: -1,
      editedItem: {
        purchaseOrderItemId: "",
        itemCustomerId: "",
        itemDepartment: null,
        itemDepartmentId: "",
        itemDepartmentName: "",
        itemCurrency: null,
        itemCurrencyName: "",
        itemCurrencyId: "",
        itemExchangeRate: "0",
        itemAmount: "0",
        itemTaxAmount: "0",
        itemTotalAmount: "0",
        itemTotalTaxAmount: "0",
        itemDescription: null,
      },
      defaultItem: {
        purchaseOrderItemId: "",
        itemCustomerId: "",
        itemDepartment: null,
        itemDepartmentId: "",
        itemDepartmentName: "",
        itemCurrencyName: "",
        itemCurrency: null,
        itemCurrencyId: "",
        itemExchangeRate: "0",
        itemAmount: "0",
        itemTaxAmount: "0",
        itemTotalAmount: "0",
        itemTotalTaxAmount: "0",
        itemDescription: null,
      },
    };
  },
  validations: {
    model: {
      purchaseOrderNo: {
        required,
        maxLength: maxLength(100),
      },
      customerOrderNo: {
        maxLength: maxLength(100),
      },
      customerId: {
        required,
        maxLength: maxLength(36),
      },
      sows: {
        required,
        maxLength: maxLength(36),
      },
      description: {
        maxLength: maxLength(255),
      },
    },
  },
  watch: {
    dialog(val) {
      if (val == true) {
        // dialog open
        this.orderItemsRenderModalComponent();
        this.editedItem.itemCustomerId = this.model.customerId;
        this.detectCurrencyInput();
      }
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? this.$t("dialog.addNew")
        : this.$t("dialog.edit");
    },
    purchaseOrderNoErrors() {
      const errors = [];
      const field = "purchaseOrderNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerOrderNoErrors() {
      const errors = [];
      const field = "customerOrderNo";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "100" }));
      return errors;
    },
    customerIdErrors() {
      const errors = [];
      const field = "customerId";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    sowsErrors() {
      const errors = [];
      const field = "sows";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].required &&
        errors.push(this.$t("v.text.required"));
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "36" }));
      return errors;
    },
    descriptionErrors() {
      const errors = [];
      const field = "description";
      if (!this.$v["model"][field].$dirty) return errors;
      !this.$v["model"][field].maxLength &&
        errors.push(this.$t("v.string.maxLength", { max: "255" }));
      return errors;
    },
  },
  created() {
    this.model.id = generateId(this);
    this.initializeInvoiceOrderItems();
    this.initializePurchaseOrderItems();
    this.initializeDepartments();
  },
  mounted() {
    this.detectCurrencyInput();
  },
  methods: {
    detectCurrencyInput() {
      if (this.editedItem.itemCurrencyId == "TRY") {
        this.money.suffix = " TL";
        this.showExchangeRate = false;
      }
      if (this.editedItem.itemCurrencyId == "EUR") {
        this.money.suffix = " EUR";
        this.showExchangeRate = true;
      }
      if (this.editedItem.itemCurrencyId == "USD") {
        this.money.suffix = " USD";
        this.showExchangeRate = true;
      }
    },
    setCustomerId(event) {
      this.editedItem.itemCustomerId = event.id;
      this.initializeDepartments(event.id);
    },
    getCustomerId() {
      return this.editedItem.itemCustomerId;
    },
    detectTotalMonths() {
      if (
        this.model.startYearId &&
        this.model.startMonthId &&
        this.model.endYearId &&
        this.model.endMonthId
      ) {
        let startDate =
          String(this.model.startYearId) +
          "-" +
          String(this.model.startMonthId) +
          "-01";
        let endDate =
          String(this.model.endYearId) +
          "-" +
          String(this.model.endMonthId) +
          "-01";
        let totalMonth = findTotalMonths(startDate, endDate);
        this.model.totalMonth = String(totalMonth);
      }
    },
    onValidate() {
      if (
        this.$v["model"]["customerId"].$invalid ||
        this.$v["model"]["purchaseOrderNo"].$invalid ||
        this.$v["model"]["customerOrderNo"].$invalid
      ) {
        this.tabs = 0;
      }
    },
    initializeDepartments(customerId = null) {
      var qs = "?";
      if (customerId) {
        qs = "?customerId=" + customerId;
      } else if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "customerId")
      ) {
        qs = "?customerId=" + this.item.customerId;
      }
      axios.get("departments/findAll" + qs).then((res) => {
        if (res.status == 200) {
          this.departments = res.data.data;
        }
      });
    },
    initializeInvoiceOrderItems() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "invoiceOrderItems")
      ) {
        this.invoiceOrderItems = JSON.parse(this.item.invoiceOrderItems);
        this.model.invoiceOrderItems = this.item.invoiceOrderItems;
      }
    },
    initializePurchaseOrderItems() {
      if (
        typeof this.item === "object" &&
        this.item !== null &&
        Object.prototype.hasOwnProperty.call(this.item, "purchaseOrderItems")
      ) {
        this.purchaseOrderItems = JSON.parse(this.item.purchaseOrderItems);
        this.model.purchaseOrderItems = this.item.purchaseOrderItems;
      }
    },
    orderItemsRenderModalComponent() {
      this.renderKeyOrderItems = this.renderKeyOrderItems + 1;
    },
    editItem(item) {
      this.orderItemsEditWindow = true;
      this.editedIndex = this.purchaseOrderItems.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.model.purchaseOrderItems = JSON.stringify(this.purchaseOrderItems);
      this.dialog = true;
      this.$store.commit(
        `orders/setDialogItem`,
        Object.assign({}, this.editedItem)
      );
    },
    deleteItem(item) {
      this.editedIndex = this.model.purchaseOrderItems.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.purchaseOrderItems.splice(this.editedIndex, 1);
      this.model.purchaseOrderItems = JSON.stringify(this.purchaseOrderItems);
      this.closeDelete();
    },
    open() {
      this.dialog = true;
      this.orderItemsEditWindow = false;
    },
    close() {
      this.dialog = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(async () => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    deleteIIitem(item) {
      this.IIeditedIndex = this.model.invoiceOrderItems.indexOf(item);
      this.dialogIIDelete = true;
    },
    deleteIIitemConfirm() {
      this.invoiceOrderItems.splice(this.IIeditedIndex, 1);
      this.model.invoiceOrderItems = JSON.stringify(this.invoiceOrderItems);
      this.closeIIDelete();
    },
    closeIIDelete() {
      this.dialogIIDelete = false;
    },
    orderItemsModalSave(editedItem) {
      this.editedItem = editedItem;
      this.save();
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(
          this.purchaseOrderItems[this.editedIndex],
          this.editedItem
        );
      } else {
        this.purchaseOrderItems.push(this.editedItem);
      }
      this.model.purchaseOrderItems = JSON.stringify(this.purchaseOrderItems);
      this.close();
    },
  },
};
</script>
